<template>
    <div class="mt-mask" data-v-mt>
        <div class="mt-wrapper" data-v-mt>
            <h1 data-v-mt><span class="logo" data-v-mt>@</span><span class="logoTxt" data-v-mt>TJC</span></h1>
            <div class="mt-content" data-v-mt>
                <h2 data-v-mt>{{ maintenance.title }}</h2>
                <div v-html="maintenance.desc"></div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name : "Maintenance",
    
    data(){
        return {
            maintenance : this.$store.getters.maintenance
        }
    },

    created(){
        // 非メンテナンスの場合
        if(!this.maintenance.flag)
        {
            // ポータルへ遷移
            this.$router.push({name:'Portal'})
        }
    }

}
</script>

<style scoped>
    .mt-mask[data-v-mt] {
        position: fixed;
        z-index: 9998;
        top: 0;
        left: 0;
        width: 100%;
        height: 120%;
        background-color:#fafafa;
        color: #5e5e5e;
        font: 400 87.5%/1.5em 'Open Sans', sans-serif;
    }
    .mt-wrapper[data-v-mt] {
        margin: 3em auto;
        padding: 0 1em;
        max-width: 800px;
        width : 90%;
    }
    .mt-content[data-v-mt]
    {
        /* background-color: #eeeeee; */
        margin: 0 auto;
        padding: 10px;
        max-width: 500px;
        width : 90%;
    }

    h1[data-v-mt]{
        text-align: center;
        font-family: 'Carme', Segoe UI, "メイリオ", Meiryo, sans-serif;
        font-size: 150%;
        margin-top:80px;
    }
    h2[data-v-mt]{
        font-size: 200%;
        margin : 30px 0px 50px 0px;
    }
    .logo[data-v-mt] {
        color: #00a7ce;
        font-weight: bolder;
        font-size: 130%;
        font-family: 'Open Sans', Segoe UI, "メイリオ", Meiryo, sans-serif;
    }
    .logoTxt[data-v-mt] {
        line-height: 100%;
        margin-left: 5px;
    }

</style>





