<template>
    <article>
        <div class="editBox" data-v-edit>
            <h2 data-v-edit>Personal Info</h2>
            <h3 data-v-edit>Login ID</h3>
            <input type="text" name="id" placeholder="半角英数字記号 必須" v-model="userId" data-v-edit>

            <table style="width: 100%;" data-v-edit>
                <tr>
                    <td colspan="2"><h3 data-v-edit>Email</h3></td>
                </tr>
                <tr>
                    <td>
                        <input type="text" name="email" placeholder="半角英数字記号 必須" v-model="email" data-v-edit>
                    </td>
                    <td>
                        <input type="checkbox" v-model="emailOpen" class='checkBoxInput' data-v-edit>
                        <label>公開</label>
                    </td>
                </tr>
                <tr>
                    <td colspan="2"><h3 data-v-edit>twitter</h3></td>
                </tr>
                <tr>
                    <td>
                        <input type="text" name="twitter" placeholder="@ユーザーID"  v-model="twitter" data-v-edit>
                    </td>
                    <td>
                        <input type="checkbox" v-model="twitterOpen" class='checkBoxInput' data-v-edit>
                        <label>公開</label>
                    </td>
                </tr>
                <tr>
                    <td colspan="2"><h3 data-v-edit>Facebook</h3></td>
                </tr>
                <tr>
                    <td>
                        <input type="url" name="facebook" placeholder="プロフィールURL"  v-model="facebook" data-v-edit>
                    </td>
                    <td>
                        <input type="checkbox" v-model="facebookOpen" class='checkBoxInput' data-v-edit>
                        <label>公開</label>
                    </td>
                </tr>
            </table>
            <p style="margin-top:20px; margin-left:10px;">※ 公開にチェックをつけた情報は、他のユーザーがプロフィールページ上で参照できます。</p>

            <h3 data-v-edit>生年月日</h3>
            <input type="date" name="dob"  v-model="dob" data-v-edit>

            <h3 data-v-edit>近況コメント</h3>
            <input type="text" name="comment"  v-model="comment" data-v-edit>


        </div>

        <div class="editBox" data-v-edit>
            <h2 data-v-edit>University Info</h2>
            <h3 data-v-edit>在学年</h3>
            <input type="date" name="univFrom" class="fromToDate" v-model="univ.from" data-v-edit>
            ～
            <input type="date" name="univTo" class="fromToDate" v-model="univ.to" data-v-edit>
            <h3 data-v-edit>学科・コース・専攻・研究室・役職等</h3>
            <table class="subinput" data-v-edit>
                <tr v-for='(dep, index) in deps' :key='index'>
                    <td>
                        <input type="text"  v-model="dep.value">
                    </td>
                    <td>
                        <input type="checkbox" v-model="dep.del" class='checkBoxInput' data-v-edit>
                        <label>削除</label>
                    </td>
                </tr>
            </table>
            <div class="buttonBase" v-on:click="addDep">行追加</div>
        </div>

        <div class="editBox" data-v-edit>
            <h2 data-v-edit>居住地の最寄</h2>
            <table class="subinput" data-v-edit>
                <tr v-for='(residence, index) in residences' :key='index'>
                    <td>
                        <p style="margin-top:20px">{{ residence.name }}</p>
                        <input type="date" class="fromToDate" v-model="residence.from" data-v-edit>～<input type="date" class="fromToDate" v-model="residence.to" data-v-edit>
                    </td>
                    <td style="min-width:80px">
                        <div class="buttonBase buttonSmall" v-on:click="editResidence(index)">地図</div>
                        <input type="checkbox" v-model="residence.del" class='posCheckBoxInput' data-v-edit>
                        <label>削除</label>
                    </td>
                </tr>
            </table>
            <div class="buttonBase" v-on:click="addResidence">行追加</div>
        </div>

        <p style="margin-bottom:20px; margin-left:10px;">※ 非公開指定している情報を除き、プロフィール情報は他のユーザーが当サイト上から参照できます。</p>
        <div class="buttonBase" v-on:click="toUpdate">更新</div>

        <Modal
            v-if="showModal"
            v-bind:mtypeIn='2'
            v-on:modal-ok-event    = "modalOk"
        >
            <p slot="header">@TJC message</p>
            <template slot="body">
                <p v-html="modalMsg" />
            </template>
        </Modal>

        <EditLoc 
            v-if="showEditLoc" 
            v-bind:pos = "residences"
            v-bind:index = "posIndex" 
            v-on:editloc-close-event="editLocClose"
        />

    </article>
</template>

<script>
import Modal    from '@/widgets/Modal'
import EditLoc  from '@/widgets/EditLocation'

export default {
    name: "EditProfile",

    components :{
        Modal,
        EditLoc
    },

    data(){
        return{
            userId : "",
            email   : "",
            emailOpen : false,
            twitter : "",
            twitterOpen : false,
            facebook: "",
            facebookOpen : false,
            comment : "",
            dob     : "",
            univ    :
            {
                from : "",
                to   : ""
            },
            deps : [],
            residences : [],

            posIndex : 0,

            modalMsg : "",
            showModal : false,

            showEditLoc : false
        }
    },

    mounted(){
        // populate
        this.userId = this.$store.getters.profile.userId
        this.comment    =   this.$store.getters.profile.comment
        this.dob        =   this.$store.getters.profile.dob
        if(this.$store.getters.profile.univ != null)
        {
            this.univ.from  =   this.$store.getters.profile.univ.from
            this.univ.to    =   this.$store.getters.profile.univ.to

            if(this.$store.getters.profile.univ.dep != null)
            {
                //  学部情報
                for(var i=0; i<this.$store.getters.profile.univ.dep.length; i++)
                {
                    var dep = {
                        value : this.$store.getters.profile.univ.dep[i],
                        del   : false
                    }
                    this.deps.push(dep)
                }
            }
        }

        for(var i=0; i<this.$store.getters.profile.contacts.length; i++)
        {
            if(this.$store.getters.profile.contacts[i].type == 'Email')
            {
                this.email      = this.$store.getters.profile.contacts[i].value
                this.emailOpen  = this.$store.getters.profile.contacts[i].open
            }
            else if(this.$store.getters.profile.contacts[i].type == 'twitter')
            {
                this.twitter        = this.$store.getters.profile.contacts[i].value
                this.twitterOpen    = this.$store.getters.profile.contacts[i].open
            }
            else if(this.$store.getters.profile.contacts[i].type == 'facebook')
            {
                this.facebook       = this.$store.getters.profile.contacts[i].value
                this.facebookOpen   = this.$store.getters.profile.contacts[i].open
            }
        }

        // 居住地の最寄
        for(var i=0; i<this.$store.getters.profile.residences.length; i++)
        {
            var residence = {
                id      :   this.$store.getters.profile.residences[i].id,
                name    :   this.$store.getters.profile.residences[i].name,
                lat     :   this.$store.getters.profile.residences[i].lat,
                lng     :   this.$store.getters.profile.residences[i].lng,
                from    :   this.$store.getters.profile.residences[i].from,
                to      :   this.$store.getters.profile.residences[i].to,
                del     :   false
            }
            this.residences.push(residence)
        }

    },

    methods:{
        // 学部レコード追加
        addDep()
        {
            this.deps.push({value : "", del:false})
        },
        // 学部レコード削除
        delDep(index)
        {
            console.log(index)
            this.$delete(this.deps, index)
        },

        // 居住地変更
        editResidence(index)
        {
            this.posIndex = index
            this.$store.dispatch('setPosSingleMap', {lat : this.$store.getters.profile.residences[index].lat, lng : this.$store.getters.profile.residences[index].lng})
            this.showEditLoc = true
        },
        // 居住地追加
        addResidence()
        {
            this.$store.dispatch('addResidence',{id:null, name:"", lat:null, lng:null, from:null, to:null})

            var reg = {name:"", from:null, to:null}
            this.residences.push(reg)
        },

        editLocClose()
        {
            this.residences = []
            for(var i=0; i<this.$store.getters.profile.residences.length; i++)
            {
                var residence = {
                    id      :   this.$store.getters.profile.residences[i].id,
                    name    :   this.$store.getters.profile.residences[i].name,
                    lat     :   this.$store.getters.profile.residences[i].lat,
                    lng     :   this.$store.getters.profile.residences[i].lng,
                    from    :   this.$store.getters.profile.residences[i].from,
                    to      :   this.$store.getters.profile.residences[i].to,
                    del     :   false
                }
                this.residences.push(residence)
            }
            this.showEditLoc = false
        },

        // 更新
        toUpdate()
        {
            console.log(this.userId)
            console.log(this.email)
            // 必須チェック
            if(this.userId == "" || this.email == "")
            {
                this.modalMsg   = "Login IDとEmailを入力してください。"
                this.showModal  = true
                return
            }

            this.$store.dispatch('updateProfile', {userId:this.userId, email:this.email, emailOpen:this.emailOpen, twitter:this.twitter, twitterOpen:this.twitterOpen, facebook:this.facebook,facebookOpen:this.facebookOpen, comment:this.comment, dob:this.dob, univ:this.univ, deps:this.deps, residences:this.residences})
        },

        modalOk(){
            this.showModal = false
        }
    }
}
</script>

<style scoped>
    .editBox[data-v-edit]{
        margin : 0px 0px 50px 0px;
    }

    table.subinput[data-v-edit]{
        width: 100%;
        margin: 0 auto;
        /* margin-bottom: 30px; */
        margin-left : 10px;
    }
    .info[data-v-edit]{
        margin: 30px 0px;
        font-size: 80%;
    }

    h2[data-v-edit] {
    font-size: 130%;
    font-weight: normal;
    color: #00a7ce;

    margin: 20px 0px 5px 0px;
    }

    h3[data-v-edit] {
    font-size: 100%;
    font-weight: normal;

    margin: 30px 0px 0px 10px;
    }

    input[data-v-edit]{
        margin: 5px 0px 0px 10px;
    }

    .checkBoxInput[data-v-edit]{
        width: unset;
        height: unset;
    }
    .posCheckBoxInput[data-v-edit]{
        width: unset;
        height: unset;
        margin : 10px 5px 10px 15px;
    }


    .buttonSmall{
        margin: 0px 15px;
    }

    input.fromToDate[data-v-edit]
    {
        width : 150px;
    }

</style>


