<template>
    <section>
        <div id="map"></div>
        <div id="mapInfo">
            <h2 data-v-portal v-if="infoMsgs.length > 0">Information</h2>
            <div v-for="(infoMsg, index) in infoMsgs" :key="index">
                <h3 data-v-portal v-html="infoMsg.title"></h3>
                <div class="info" data-v-portal v-html="$sanitize(infoMsg.body)"></div>
            </div>
            <hr  v-if="infoMsgs.length > 0">
            
            <h2 data-v-portal>What's new</h2>
            <ul>
                <li v-for="(news, index) in newsData" :key="index">
                    <template v-if="news.type=='1'"><router-link v-bind:to="news.userUrl">{{ news.userName }}</router-link>{{ news.str }} {{news.creationtime}}</template>
                    <template v-if="news.type=='2'"><router-link v-bind:to="news.userUrl">{{ news.userName }}</router-link>{{ news.str }} <router-link v-bind:to="news.eventUrl">{{ news.eventName }}</router-link> {{ news.str2 }} {{news.creationtime}}</template>
                </li>
            </ul>

            <div style="margin: 40px 0px;">
                <h2 data-v-portal>Comment TimeLine</h2>
                <TimeLineComp 
                    v-bind:limit="5"
                />
                <p style="text-align:center; margin:10px 0px;"><router-link to='/timeline'>Read More</router-link></p>
            </div>

        </div>
    </section>
</template>

<script>
import TimeLineComp from '@/components/TimeLineComp'

export default {
    name : "Portal",

    components :{
        TimeLineComp
    },
    
    data(){
        return {
            map         :   null,
            marker      :   [],
            infoWindow  :   [],

            newsData    :   [],

            infoMsgs : this.$store.getters.portalInfos
        }
    },

    //  初期化
    mounted(){
        console.log("info:portal描画")

        // 更新情報populator
        this.newsData = []
        for(var i=0; i<this.$store.getters.portalNews.length; i++)
        {
            // populate
            var newsString = {
                type : this.$store.getters.portalNews[i].type,
                userName : this.$store.getters.portalNews[i].userName,
                userUrl  : '/profile/' + this.$store.getters.portalNews[i].userNo,
                eventName : this.$store.getters.portalNews[i].eventName + "（" + this.$store.getters.portalNews[i].date + "）",
                eventUrl : '/events/detail/' + this.$store.getters.portalNews[i].eventNo,
                str      : "",
                str2     : "",
                creationtime : this.$store.getters.portalNews[i].creationtime
            }

            // 最寄変更
            if(this.$store.getters.portalNews[i].type == "1")
            {
                newsString.str = " さんが、最寄に " + this.$store.getters.portalNews[i].posName + " を追加しました。"
            }
            // イベント
            else if(this.$store.getters.portalNews[i].type == "2")
            {
                newsString.str = "さんが、"
                if(this.$store.getters.portalNews[i].posName != null && this.$store.getters.portalNews[i].posName != "")
                {
                    newsString.str += this.$store.getters.portalNews[i].posName + " に "
                }
                newsString.str2 = " をイベント追加しました。"
            }
            this.newsData.push(newsString)
        }

        //  地図描画
        var mapLatLng = new google.maps.LatLng({lat: 35.4644342, lng: 136.7389885});
        this.map = new google.maps.Map(document.getElementById('map'), {
            center: mapLatLng,
            zoom: 7
        });

        // マーカー毎の処理
        for (var i = 0; i < this.$store.getters.portalMarkers.length; i++) {
            var markerLatLng = new google.maps.LatLng({lat: this.$store.getters.portalMarkers[i].lat, lng: this.$store.getters.portalMarkers[i].lng}); // 緯度経度のデータ作成
            this.marker[i] = new google.maps.Marker({ // マーカーの追加
                position: markerLatLng, // マーカーを立てる位置を指定
                map: this.map // マーカーを立てる地図を指定
            });
    
            let profUrl = location.href + 'profile/' + this.$store.getters.portalMarkers[i].userNo
            let eventUrl = location.href + 'events/detail/' + this.$store.getters.portalMarkers[i].eventNo

            if(this.$store.getters.portalMarkers[i].type == "1")
            {
                this.infoWindow[i] = new google.maps.InfoWindow({ // 吹き出しの追加
                    content: '<div class="mapInfo"><p class="markerTitle">' + this.$store.getters.portalMarkers[i].name + '</p><ul><li><a href="' +  profUrl + '">' + this.$store.getters.portalMarkers[i].userName +'</a> さんの最寄</li></ul></div>' // 吹き出しに表示する内容
                });
            }
            else if(this.$store.getters.portalMarkers[i].type == "2")
            {
                this.infoWindow[i] = new google.maps.InfoWindow({ // 吹き出しの追加
                    content: '<div class="mapInfo"><p class="markerTitle">' + this.$store.getters.portalMarkers[i].name + '</p><ul><li>オーナー：<a href="' +  profUrl + '">' + this.$store.getters.portalMarkers[i].userName +'</a> さん</li><li>日時：' + this.$store.getters.portalMarkers[i].date +'</li><li><a href="' + eventUrl + '">イベント詳細</a></li></ul></div>' // 吹き出しに表示する内容
                });
            }
    
            this.markerEvent(i); // マーカーにクリックイベントを追加
        }
    },

    methods:{
        markerEvent(i) {
            var self = this
            this.marker[i].addListener('click', function() { // マーカーをクリックしたとき
                self.infoWindow[i].open(self.map, self.marker[i]); // 吹き出しの表示
            });
        }
    }
}
</script>

<style scoped>
/*--  Start of map section  --*/
    #map {
        height: 500px;
        width: 100%;
    }

    #mapInfo{
        padding: 5px 10px 10px 10px;
        display: block;
    }

    #mapInfo li{
        font-size: 90%;
    }

    h2 {
        font-size:120%;
        margin-top: 10px;
        margin-bottom: 5px;
    }
/*--  End of map section  --*/

/* -- Start of Info block */
    h3[data-v-portal] {
        font-size:105%;
        font-weight: 100;
        margin:0px;
    }

    .info[data-v-portal]{
        font-size:90%;
        margin:5px 0px 30px 10px;
    }

/* -- End of Info block */
</style>



