<template>
    <section>
        <!-- <InputInvitation v-if=true />
        <RegInput v-if=false />
        <CompReg v-if=false /> -->
        <router-view />
    </section>
</template>

<script>
import InputInvitation   from '@/components/InputInvitation'
import RegInput          from '@/components/RegInput'
import CompReg           from '@/components/CompReg'

export default {
    name : "Registration",
    created: function(){
        // hide toggre
        this.$store.dispatch('setToggreState', false)
    },

    components :{
        InputInvitation,
        RegInput,
        CompReg
    }
}
</script>

<style>
    section[data-v-registration]{
        margin-top: 30px;
    }

    input[data-v-registration]{
        margin:30px 0px 30px 0px;
    }
</style>



