<template>
    <section>
        <h1 data-v-inquiry>お問い合わせフォーム</h1>
        <h2 data-v-inquiry>件名</h2>
        <input type="text" v-model="inputData.subject" data-v-inquiry>
        <h2 data-v-inquiry>本文</h2>
        <textarea rows="5" v-model="inputData.body" data-v-inquiry></textarea>

        <div class="buttonBase" v-on:click="toSend">送信</div>

        <Modal
            v-if="showModal"
            v-bind:mtypeIn='2'
            v-on:modal-ok-event    = "modalOk"
        >
            <p slot="header">@TJC message</p>
            <template slot="body">
                <p v-html="modalMsg" />
            </template>
        </Modal>

    </section>
</template>

<script>
import Profile   from '@/components/Profile'
import Modal     from '@/widgets/Modal'

export default {
    name : "Inquiry",

    components :{
        Modal
    },

    data(){
        return{
            inputData : {
                subject : "",
                body : ""
            },
            modalMsg : "",
            showModal : false,
        }
    },

    methods:{
        async toSend(){
            if(this.inputData.subject == "")
            {
                this.modalMsg = "件名を入力してください"
                this.showModal = true
                return
            }
            else if(this.inputData.body == "")
            {
                this.modalMsg = "本文を入力してください"
                this.showModal = true
                return
            }

            await this.$store.dispatch('sendInquiry', this.inputData)

            this.inputData.subject = ""
            this.inputData.body = ""

        },
        modalOk(){
            this.showModal = false
        }
    }
}
</script>

<style scoped>
    h1[data-v-inquiry] {
        font-size: 130%;
        font-weight: normal;
        color: #00a7ce;

        margin: 20px 0px 5px 0px;
    }

    h2[data-v-inquiry] {
        font-size: 100%;
        font-weight: normal;

        margin: 30px 0px 0px 10px;
    }

    input[data-v-inquiry]{
        margin: 5px 0px 0px 10px;
    }

    textarea[data-v-inquiry]{
        margin: 5px 0px 20px 10px;
    }
</style>



