<template>
  <transition name="modal">
    <div class="modal-mask">
        <div class="modal-wrapper">
        <div class="modal-container">

            <div class="modal-header">
            <slot name="header">
                default header
            </slot>
            </div>

            <div class="modal-body">
            <slot name="body">
                default body
            </slot>
            </div>

            <div class="modal-footer">
                <div class="mtype1" v-show="this.mtypeIn == 1">
                  <div class='buttonBase' style='float:left; margin:10px; width:75px' v-on:click = "clickYes">Yes</div>
                  <div class='buttonBase' style='float:left; margin:10px; width:75px' v-on:click = "clickNo">No</div>
                </div>
                <div class="mtype2" v-show="this.mtypeIn == 2">
                  <div class='buttonBase' style='float:left; margin:10px; width:75px' v-on:click = "clickOk">OK</div>
                </div>
            </div>
        </div>
        </div>
    </div>
  </transition>
</template>

<script>
  export default {
    name: "Modal",

    props: ['mtypeIn'],

    methods:{
      clickYes(){
        this.$emit('modal-yes-event')
      },
      clickNo(){
        this.$emit('modal-no-event')
      },
      clickOk(){
        this.$emit('modal-ok-event')
      }
    }
  }
</script>


<style scoped>
 .modal-mask {
    position: fixed;
    z-index: 9999;
    top: 0;
    left: 0;
    width: 100%;
    height: 120%;
    background-color: rgba(0, 0, 0, .5);
    display: table;
    transition: opacity .3s ease;
  }
  
  .modal-wrapper {
    display: table-cell;
    vertical-align: middle;
  }
  
  .modal-container {
    width: 95%;
    max-width: 350px;
    margin: 0px auto;
    background-color: #fff;
    border-radius: 2px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
    transition: all .3s ease;
    /* font-family: Helvetica, Arial, sans-serif; */
  }

  /*mobile vertical*/
  @media (max-width:710px) and (min-height:500px){
      .modal-container {
            margin-top:-50%;
      }
  }
  /*mobile horizontal*/
  @media (max-width:710px) and (max-height:500px){
      .modal-container {
            margin-top:-10%;
      }
  }
  /*desktop*/
  @media (min-width:710px){
      .modal-container {
            margin-top:-20%;
      }
  }

  
  .modal-header {
    padding: 1px 0px 1px 15px;
    color: #FFFFFF;
    background-color : #00a7ce;
  }
  
  .modal-body {
    margin: 20px 0px 0px 0px;
    padding: 0px 30px;
  }

  .modal-footer{
    padding: 0px 30px 20px 30px;
    overflow: hidden;
  }

  .mtype1{
    margin: 0 auto;
    width:220px;
    overflow: hidden;
  }
  
  .mtype2{
    margin: 0 auto;
    width:100px;
    overflow: hidden;
  }

  .modal-default-button {
    float: right;
  }
  
  .modal-enter {
    opacity: 0;
  }
  
  .modal-leave-active {
    opacity: 0;
  }
  
  .modal-enter .modal-container,
  .modal-leave-active .modal-container {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
  }
</style>


