<template>
    <section>
        <h1>パスワード再設定</h1>
        <p>会員登録されているメールアドレスを入力し「送信」をクリックしてください。</p>
        <p>入力されたメールアドレスに、std.ink.system@gmail.com から確認メールを送信します。</p>
        <p>std.ink.system@gmail.com からメール受信できるよう、メール設定をご確認ください。</p>

        <input type="text" name="mail" placeholder="登録メールアドレス" v-model="email" style="margin:20px 0px;">

        <div class="buttonBase" v-on:click="toSent">送信</div>
    </section>
</template>

<script>
export default {
    name : "PasswordReminder",
    created: function(){
        // hide toggre
        this.$store.dispatch('setToggreState', false)
    },

    data(){
        return{
            email:null
        }
    },

    methods:{
        toSent(){
            // URL生成
            let url = process.env.RootUrl + "#/resetpassword?key="
            this.$store.dispatch('passwordReminder', {email:this.email, url:url})
        }
    }
}
</script>


