<template>
    <div style="position: fixed;
        z-index: 9998;
        top: 0;
        left: 0;
        width: 200%;
        height: 150%;
        background-color: rgba(0, 0, 0, .5);
        display: table;"
        v-on:click="closeMenu">

        <div id="menu" data-v-sidemenuF>
            <h1 class="userId" data-v-sidemenuF>{{ userId }}</h1>
                <h2 data-v-sidemenuF><router-link to='/'>Portal</router-link></h2>
                <h2 data-v-sidemenuF><router-link :to="{name: 'MyPage'}">マイページ</router-link></h2>
                <h2 data-v-sidemenuF><a href="#" v-on:click="logout">ログアウト</a></h2>

            <h1 data-v-sidemenuF><span class="logo">@</span><span class="logoTxt">TJC</span></h1>
                <h2 data-v-sidemenuF><router-link :to="{name : 'MemberList'}">メンバーリスト</router-link></h2>
                <h2 data-v-sidemenuF><router-link :to="{ name: 'EventList'}">イベント一覧</router-link></h2>
                <h2 data-v-sidemenuF><router-link :to="{ name: 'EntryList'}">エントリ一覧</router-link></h2>
                <h2 data-v-sidemenuF><router-link :to="{ name: 'Inquiry'}">お問い合わせフォーム</router-link></h2>
                <h2 data-v-sidemenuF><router-link :to="{ name: 'Terms'}">利用規約</router-link></h2>
                
            <h1 data-v-sidemenuF>外部リンク</h1>
                <h2 data-v-sidemenuF><a href="https://gifukoujichikai.tumblr.com/" target="_blank">WEB自治会室</a></h2>
                <h2 data-v-sidemenuF><a href="https://tjc-word-library.herokuapp.com/libraries/" target="_blank">用語名言集</a></h2>
            
            <template v-if="admin">
                <h1 data-v-sidemenuF>Admin</h1>
                    <h2 data-v-sidemenuF><router-link :to="{name:'AdminInfo'}">管理画面</router-link></h2>
            </template>
        </div>
    </div>
</template>

<script>
export default {
    name : 'SideMenu',

    computed : {
        userId()
        {
            return this.$store.getters.userInfo.userId
        },
        admin()
        {
            return this.$store.getters.userInfo.admin
        }
    },

    data: function () {
        return {
        }
    },
    methods : {
        closeMenu(e){
            this.$store.dispatch('setShowSidebar', false)
        },
        nonClose(){},

        logout(){
            this.$store.dispatch('callLogoutApi')
        }
        
    }
}
</script>

<style scoped>
    #menu[data-v-sidemenuF]{
        /* margin : 15px; */
        background-color: #fafafa;
        height:150%;
        width:250px;

        /* margin-top:-10px; */
        padding:10px 15px;
    }

    #menu h1[data-v-sidemenuF]{
        font-size: 120%;
        font-weight: 500;
        margin: 25px 0px 5px 10px;
    }

    #menu h2[data-v-sidemenuF]{
        font-size: 100%;
        font-weight: 300;
        margin: 10px 0px 10px 30px;
    }

    .logo {
        color: #00a7ce;
        font-weight: bolder;
        /* font-size: 130%; */
        font-family: 'Open Sans', Segoe UI, "メイリオ", Meiryo, sans-serif;
    }
    .logoTxt {
        font-size: 110%;
    }

    /* #menu .selected[data-v-sidemenuF]{
        color: #f16272;
    } */
</style>





