<template>
  <div class="profile" data-v-profile>
    <h1 data-v-profile>{{ profile.userId }}</h1>
    <h2 data-v-profile v-if="profile.contacts != null && profile.contacts.length>0">連絡先</h2>
    <ul>
      <li v-for="(contact, index) in profile.contacts" :key="index">
        {{ contact.type }} :
        <a
          v-bind:href="contact.url"
          v-if="contact.url != null"
          target="_blank"
        >{{ contact.value }}</a>
        <template v-if="contact.url == null">{{ contact.value }}</template>
      </li>
    </ul>

    <h2 data-v-profile>Personal Info</h2>
    <div class="personalBlock" data-v-profile>
      <div class="personalInfo" data-v-profile>
        <h3 data-v-profile v-if="profile.dob != null">生年月日</h3>
        <ul data-v-profile>
          <li data-v-profile>{{ profile.dob }}</li>
        </ul>
      </div>

      <div class="personalInfo" data-v-profile>
        <h3 data-v-profile  v-if="profile.residences != null && profile.residences.length > 0">居住地の最寄</h3>
        <ul data-v-profile>
          <li
            v-for="(residence, index) in profile.residences"
            :key="index"
            data-v-profile
          >{{ residence.name }} ({{ residence.from }} ～ {{ residence.to }})</li>
        </ul>
      </div>

      <div class="personalInfo" data-v-profile>
        <h3 data-v-profile v-if="profile.univ != null">在学年</h3>
        <ul data-v-profile>
          <li data-v-profile v-if="profile.univ != null">{{ profile.univ.from }} ～ {{ profile.univ.to }}</li>
        </ul>
      </div>

      <div class="personalInfo" data-v-profile>
        <h3 data-v-profile v-if="profile.univ != null">学科・コース・専攻・研究室・役職等</h3>
        <ul data-v-profile  v-if="profile.univ != null">
          <li v-for="(dep, index) in profile.univ.dep" :key="index" data-v-profile>{{ dep }}</li>
        </ul>
      </div>
    </div>
    <h2 data-v-profile v-if="profile.comment != null">Comment</h2>
    <p class="comment" data-v-profile>{{ profile.comment }}</p>
  </div>
</template>

<script>
export default {
  name: "Profile",
  computed: {
    profile() {
      return this.$store.getters.profile;
    }
  }
};
</script>

<style scoped>
.profile[data-v-profile]{
    margin-bottom:30px;
}

h1[data-v-profile] {
  font-size: 150%;
}

h2[data-v-profile] {
  font-size: 110%;
  font-weight: normal;
  color: #00a7ce;

  margin: 20px 0px 10px 0px;
}

h3[data-v-profile] {
  font-size: 100%;
  font-weight: normal;

  margin: 5px 0px;
}

ul li[data-v-profile] {
  font-size: 100%;
}

ul[data-v-profile] {
  margin: 0px 0px 0px 5px;
}

ul li[data-v-profile]:before {
  content: "- ";
  color: #737373;
  font-weight: bold;

  margin: 5px 0px;
}

.personalBlock {
  width: 100%;
  height: auto;
  overflow: hidden;
}

.personalInfo {
  width: 40%;
  min-width: 300px;
  float: left;
  margin: 0px 0px 20px 10px;
}

.comment[data-v-profile]
{
  margin: 0px 0px 0px 10px;
}

</style>




