<template>
    <div class="login-mask" data-v-login>
        <div class="form-wrapper" data-v-login>
            <h1 data-v-login><span class="logo" data-v-login>@</span><span class="logoTxt" data-v-login>TJC</span></h1>
            <form data-v-login>
                <div class="form-item" data-v-login>
                    <label for="id"></label>
                    <input type="text" name="id" required="required" placeholder="Login ID or Email" v-model="loginId" data-v-login />
                </div>

                <div class="form-item" data-v-login>
                    <label for="password"></label>
                    <input type="password" name="password" required="required" placeholder="Password" v-model="password" v-on:keyup.enter="login" data-v-login />
                </div>

                <div class="button-panel" data-v-login>
                    <input type="button" class="button" title="Login" value="Login" v-on:click="login" data-v-login />
                    <input type="button" class="button" title="Registration" value="会員登録" v-on:click="registration" data-v-login />
                    <p style="margin:20px 0px; text-align:center;"><router-link :to="{name: 'PasswordReminder'}">loginID・パスワードを忘れた方</router-link></p>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
export default {
    name : "Login",
    
    data(){
        return {
            loginId     : null,
            password    : null
        }
    },

    methods:{
        //---------------------------------
        //  ログインボタン
        //---------------------------------
        login(){
            this.$store.dispatch('callLoginApi', {id:this.loginId, pw:this.password})
        },

        //---------------------------------
        //  会員登録ボタン
        //---------------------------------
        registration(){
            this.$router.push('/registration')
        }        
    }
}
</script>


<style scoped>
    .login-mask[data-v-login] {
        position: fixed;
        z-index: 9998;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color:#e9e9e9;
        color: #5e5e5e;
        font: 400 87.5%/1.5em 'Open Sans', sans-serif;
        /* display: table; */
        /* transition: opacity .3s ease; */
    }

    .form-wrapper[data-v-login] {
        background: #fafafa;
        margin: 3em auto;
        padding: 0 1em;
        max-width: 370px;
        width : 90%;
    }

    /* h1[data-v-login] {
        text-align: center;
        padding: 1em 0;
    } */

    h1[data-v-login]{
        text-align: center;
        font-family: 'Carme', Segoe UI, "メイリオ", Meiryo, sans-serif;
        font-size: 150%;
        padding: 1.5em 0;
        /* margin: 25px 0px 15px 0px; */
    }
    .logo[data-v-login] {
        color: #00a7ce;
        font-weight: bolder;
        font-size: 130%;
        font-family: 'Open Sans', Segoe UI, "メイリオ", Meiryo, sans-serif;
    }
    .logoTxt[data-v-login] {
        line-height: 100%;
        margin-left: 5px;
    }

    form[data-v-login] {
        padding: 0 1.5em;
        padding-bottom: 10px;
    }

    .form-item[data-v-login] {
        margin-bottom: 0.75em;
        width: 100%;
    }

    .form-item input[data-v-login] {
        background: #fafafa;
        border: none;
        border-bottom: 2px solid #e9e9e9;
        color: #666;
        font-family: 'Open Sans', sans-serif;
        font-size: 1em;
        height: 50px;
        transition: border-color 0.3s;
        width: 100%;
    }

    .form-item input:focus[data-v-login] {
        border-bottom: 2px solid #c0c0c0;
        outline: none;
    }

    .button-panel[data-v-login] {
        margin: 40px 0px;
        width: 100%;
    }

    .button-panel .button[data-v-login] {
        color: #099acb;
        border: 1px #099acb solid;
        border-radius: 5px;
        cursor: pointer;
        height: 35px;
        font-family: 'Open Sans', sans-serif;
        font-size: 1.2em;
        letter-spacing: 0.05em;
        text-align: center;
        text-transform: uppercase;
        transition: background 0.3s ease-in-out;
        width: 100%;
    }

    .button[data-v-login]{
        margin: 10px 0px;
    }

    .button:hover[data-v-login] {
        color: #BBDEFB;
        border: 1px #BBDEFB solid;
    }

</style>


