<template>
    <section>
        <h1>編集</h1>
        <EntryForm />

        <p style="margin-top:30px;"><input type="checkbox" v-model="delImg" style="width:unset; height:unset;"><label>添付済の画像を削除</label></p>

        <div class="buttonBase" v-on:click="updateEntry" style="margin-top:30px;">更新</div>

        <Modal
            v-if="showModal"
            v-bind:mtypeIn='2'
            v-on:modal-ok-event = "showModal = false"
        >
            <p slot="header">@TJC message</p>
            <template slot="body">
                <p v-html="modalMsg" />
            </template>
        </Modal>

    </section>
</template>

<script>
import EntryForm from '@/components/EntryForm'
import Modal     from '@/widgets/Modal'

export default {
    components :{
        EntryForm,
        Modal
    },

    name : "EditEntry",
    data(){
        return {
            modalMsg : "",
            showModal : false,
            delImg : false
        }
    },

    computed: {
        entry:{
            get()
            {
                return this.$store.getters.entry
            },
            set(value)
            {
                this.$store.dispatch('setEntry', value)
            }
        }
    },

    mounted(){
        if(!this.entry.editable)
        {
            this.$router.push({name:'EntryList'})
        }
    },

    methods:{
        updateEntry()
        {
            if(!this.validate())
            {
                return
            }

            if(this.delImg)
            {
                this.entry.imgUrl = ""
            }

            this.$store.dispatch('updateEntry', this.entry)

        },

        validate()
        {
            // タイトル必須
            if(this.entry.title == "")
            {
                this.modalMsg = "タイトルを入力してください"
                this.showModal = true
                return false
            }
            // 本文必須
            if(this.entry.body == "")
            {
                this.modalMsg = "本文を入力してください"
                this.showModal = true
                return false
            }

            return true
        },

    }
}
</script>

<style scoped>

</style>


