<template>
    <div>
        <nav>
            <div class="btnBox">
                <router-link :to="{name : 'AdminInfo'}" class="btn" data-v-adminmenu>Info</router-link>
                <router-link :to="{name : 'MaintenanceSetting'}" class="btn" data-v-adminmenu>メンテナンス</router-link>
                <router-link :to="{name : 'LoginedUsers'}" class="btn" data-v-adminmenu>ユーザー管理</router-link>
            </div>
        </nav>
    <section>
        <router-view />

    </section>
    </div>
</template>

<script>
import MaintenanceSetting   from    '@/components/MaintenanceSetting'
import LoginedUser          from    '@/components/LoginedUser'

export default {
    name : "AdminMenu",

    components :{
        MaintenanceSetting,
        LoginedUser
    },

    computed: {
    },

    created()
    {        
        // 管理者チェック
        if(!this.$store.getters.userInfo.admin)
        {
            this.$router.push('/')
        }
    },

    methods:{

    },
    
    data: function () {
        return {
        }
    }
}
</script>

<style scoped>
    h2[data-v-adminmenu] {
        font-size: 110%;
        font-weight: normal;
        color: #00a7ce;

        margin: 20px 0px 10px 0px;
    }

    h3[data-v-adminmenu] {
        font-size: 100%;
        font-weight: normal;

        margin: 30px 0px 0px 0px;
    }
    
    .editBox[data-v-adminmenu]{
        margin-bottom: 70px;
    }

    th[data-v-adminmenu]{
        text-align: left;
        font-weight: normal;
        padding : 5px 0px;
    }

    td[data-v-adminmenu] {
        padding:10px 10px;
    }

    .router-link-exact-active[data-v-adminmenu] {
        border-bottom: solid 3px #00a7ce;
    }

    /* .router-link[data-v-adminmenu]{
        text-decoration: none;
        color : #737373;
    } */


</style>


