import Vue          from 'vue'
import Router       from 'vue-router'
import store        from '@/store'
import HelloWorld   from '@/components/HelloWorld'
import Portal       from '@/views/Portal'
import Registration from '@/views/Registration'
import Login        from '@/views/Login'
  import InputInvitation  from '@/components/InputInvitation'
  import RegInput         from '@/components/RegInput'
  import CompReg          from '@/components/CompReg'
import  AdminLogin  from '@/views/AdminLogin'
import MyPage       from '@/views/MyPage'
  import MyProfile        from '@/components/MyProfile'
  import EditProfile      from '@/components/EditProfile'
  import UpdatePassword   from '@/components/PasswordUpdate'
import ProfileView  from '@/views/ProfileView'
import Inquiry      from '@/views/Inquiry'
import Members      from '@/views/Members'
import Events       from '@/views/Events'
import Event        from '@/views/Event'
import RegEvent     from '@/views/RegEvent'
import Maintenance  from '@/views/Maintenance'
import Terms        from '@/widgets/Terms'
import Entry        from '@/views/Entry'
import RegEntry     from '@/views/RegEntry'
import EditEntry    from '@/views/EditEntry'
import Entries      from '@/views/Entries'
import AdminMenu    from '@/views/AdminMenu'
import TimeLine     from '@/views/TimeLine'
import PasswordReminder from '@/views/PasswordReminder'
import ResetPassword from '@/views/ResetPassword'
import MaintenanceSetting  from '@/components/MaintenanceSetting'
import LoginedUser  from  '@/components/LoginedUser'
import AdminInfo    from  '@/components/AdminInfo'
import AdminInfoDtl from  '@/components/AdminInfoDtl'
import Invitation   from  '@/views/Invitation'
import GuestEvent   from  '@/views/GuestEvent'

Vue.use(Router)

// export default new Router({
  const router = new Router({
  routes: [
    // ポータル
    {
      path: '/',
      name: 'Portal',
      component: Portal,
      meta: 
      {
        requiresAuth : true
      },
      beforeEnter:async function(to, from, next)
      {
        await store.dispatch('initPortalPage')
        next()
      }
    },

    //  会員登録
    {
      path: '/registration',
      component: Registration,
      meta:
      {
        title : "会員登録"
      },

      children:[
        {
          name: 'reg-inv',
          path: '',
          component: InputInvitation,
          meta:
          {
            title : "招待コード入力"
          }
        },
        {
          name: 'reg-input',
          path: 'input',
          component: RegInput,
          meta:
          {
            title : "会員登録"
          },
          beforeEnter(to, from, next)
          {
            store.dispatch('CheckInvitationCode')
            next()
          }
        },
        {
          name: 'reg-comp',
          path: 'comp',
          component: CompReg,
          meta:
          {
            title : "会員登録完了"
          },
          beforeEnter(to, from, next)
          {
            store.dispatch('Registration')
            next()
          }
        }
      ]

    },

    // ログイン
    {
      path: '/login',
      name: 'Login',
      component: Login,
      meta:
      {
        title : "ログイン"
      },

      // ログイン済みの場合、topへ
      beforeEnter(to, from, next)
      {
        if(store.getters.logined)
        {
          next('/')
        }
        else{
          next()
        }
      }
    },
    // メンテナンス期間中管理者ログイン
    {
      path: '/admin/login',
      name: 'adLogin',
      component: AdminLogin,
      meta:
      {
        title : "Adminログイン",
        maintenance:true
      },

      // ログイン済みの場合、topへ
      beforeEnter(to, from, next)
      {
        if(store.getters.logined)
        {
          next('/')
        }
        else{
          next()
        }
      }
    },

    // マイページ
    {
      name : 'MyPage',
      path : '/mypage',
      component: MyPage,
      meta: 
      {
        requiresAuth : true,
        title : "マイページ"
      },
      children:[
        // マイページTOP
        {
          name: 'MyPage',
          path: '',
          component: MyProfile,
          meta:
          {
            title : "マイページ"
          },

          beforeEnter(to, from, next)
          {
            store.dispatch('initMypage')
            next()
          }
        },
        // プロフィール編集
        {
          name: 'EditProfile',
          path: 'edit',
          component : EditProfile,
          meta:
          {
            title : "プロフィール編集"
          },

          // プロフィールデータ未読み込みの場合はマイページへ
          beforeEnter(to, from, next)
          {
            if(store.getters.profile.userId == null)
            {
              next('/mypage')
            }
            else{
              next()
            }
          }
        },
        // パスワード変更
        {
          name : 'UpdatePassword',
          path:'password',
          component : UpdatePassword,
          meta:
          {
            title : "パスワード変更"
          }
        }
      ]
    },

    // プロフィール
    {
      name : 'Profile',
      path : '/profile/:userNo',
      component: ProfileView,
      meta: 
      {
        requiresAuth : true,
        title : "プロフィール"
      },

      beforeEnter(to, from, next)
      {
        store.dispatch('initProfile', to.params.userNo)
        next()
      }
    },

    // お問い合わせフォーム
    {
      path : '/inquiry',
      name: 'Inquiry',
      component : Inquiry,
      meta:
      {
        requiresAuth : true,
        title : "お問い合わせフォーム"
      }
    },

    // メンバーリスト
    {
      name : 'MemberList',
      path : '/members',
      component : Members,
      meta:
      {
        requiresAuth : true,
        title : "メンバーリスト"
      }
    },

    // イベントリスト
    {
      name : 'EventList',
      path : '/events',
      component : Events,
      meta:
      {
        requiresAuth : true,
        title : "イベント一覧"
      }
    },

    // イベント
    {
      name : 'EventDetail',
      path : '/events/detail/:eventNo',
      component : Event,
      meta:
      {
        requiresAuth : true,
        title : "イベント"
      }
    },

    // イベント登録
    {
      name : 'RegEvent',
      path : '/events/registration',
      component : RegEvent,
      meta:
      {
        requiresAuth : true,
        title : "イベント登録"
      }
    },

    // イベント更新
    {
      name : 'UpdateEvent',
      path : '/events/update/:eventNo',
      component : RegEvent,
      meta:
      {
        requiresAuth : true,
        title : "イベント更新"
      }
    },

    // メンテナンスページ
    {
      name: 'Maintenance',
      path: '/maintenance',
      component : Maintenance,
      meta : 
      {
        maintenance : true
      }
    },

    // 利用規約
    {
      name: 'Terms',
      path: '/terms',
      component: Terms,
      meta : 
      {
        requiresAuth : true,
        title: "利用規約"
      }
    },

    // エントリ一覧
    {
      name:  'EntryList',
      path : '/entries',
      component : Entries,
      meta:
      {
        requiresAuth : true,
        title : "エントリ一覧"
      }
    },
    // エントリ詳細
    {
      name:  'Entry',
      path : '/entries/detail/:entryNo',
      component : Entry,
      meta:
      {
        requiresAuth : true
      }
    },
    // エントリ登録
    {
      path : '/entries/registration',
      component : RegEntry,
      meta:
      {
        requiresAuth : true,
        title : "新規エントリ投稿"
      }
    },
    // エントリ編集
    {
      name : 'EditEntry',
      path : '/entries/edit',
      component : EditEntry,
      meta:
      {
        requiresAuth : true,
        title : "エントリ編集"
      }
    },


    // TimeLine
    {
      path : '/timeline',
      name : 'TimeLine',
      component : TimeLine,
      meta:
      {
        requiresAuth : true,
        title : "コメント"
      }
    },

    // AdminMenu
    {
      path: '/admin',
      component : AdminMenu,
      name : 'AdminMenu',
      meta:
      {
        requiresAuth : true,
        title : "Admin"
      },
      children:[
        {
          name: 'AdminInfo',
          path: '',
          component: AdminInfo,
          meta:
          {
            title : "Admin"
          }
        },
        {
          name: 'AdminInfoDtl',
          path: 'info/:infoId',
          component: AdminInfoDtl,
          title : "Admin Information"
        },
        {
          name: 'RegAdminInfo',
          path: 'regInfo/',
          component: AdminInfoDtl,
          title : "Admin Information登録"
        },
        
        {
          name: 'MaintenanceSetting',
          path: 'maintenance',
          component: MaintenanceSetting,
          meta:
          {
            title : "Admin メンテナンス設定"
          }
        },
        {
          name: 'LoginedUsers',
          path: 'loginedusers',
          component: LoginedUser,
          meta:
          {
            title : "Admin ログインユーザー情報"
          }
        }
      ]

    },

    // Password Reminder
    {
      path: '/passwordreminder',
      component : PasswordReminder,
      name : 'PasswordReminder',
      meta:
      {
        requiresAuth : false,
        title : "パスワード再設定"
      }
    },

    // ResetPassword
    {
      path: '/resetpassword',
      component : ResetPassword,
      name : 'ResetPassword',
      meta:
      {
        requiresAuth : false,
        title : "パスワード再設定"
      }
    },

    // ユーザー招待
    {
      path: '/invitation',
      component : Invitation,
      name : "Invitation",
      meta:{
        requiresAuth : true,
        title : "ユーザーを招待する"
      }
    },

    // ゲストイベント
    {
      name : 'GuestEventDetail',
      path : '/guest/events/detail/:eventNo',
      component : GuestEvent,
      meta:
      {
        requiresAuth : false,
        title : "イベント"
      }
    },

  ]
})

router.beforeEach((to, from, next) => async function(){
  store.dispatch('setShowSidebar', false)
  store.dispatch('setLoading', true)

  console.log('info:' + to.path)
  if(to.path != '/login' && to.path != '/admin/login')
  {
    store.dispatch('setAfterLogin', to)
  }

  // PlaceUnknownメンテナンスチェック
  await store.dispatch('CheckMaintenance')
  if((store.getters.maintenance.flag) && to.matched.some(record => !record.meta.maintenance)) 
  {
    next({name:'Maintenance'})
  }
  // else if(!(store.getters.maintenance.flag) && to.matched.some(record => record.meta.maintenance))
  // {
  //   next({name:'Portal'})
  // }

  if(to.matched.some(record => record.meta.requiresAuth))
  {
    console.log('info:need auth')

    // ログインチェック
    await store.dispatch('checkLogin')

    // ログイン済み
    if(store.getters.logined)
    {
      store.dispatch('setLoading', false)
      console.log('info:logined')
      next()
      return
    }
    else{
      store.dispatch('setLoading', false)
      // 未ログイン
      console.log('info:not logined')
      next({name:'Login'})
    }
  }
  else{
    store.dispatch('setLoading', false)
    next()
  }
}())

router.afterEach((to,from) =>
  {
    if(to.meta.title != null)
    {
      document.title = to.meta.title + " - " + process.env.PageTitle
    }
    else
    {
      document.title = process.env.PageTitle + " - " + process.env.PageDesc
    }
    
  }
)

export default router


