<template>
    <div>
        <p>@TJCに招待したいユーザーのメールアドレスを入力してください。</p>
        <p>指定されたユーザーへ招待URLリンクが送信されます。</p>
        <p>入力されたメッセージは招待メール本文に設定されます。</p>

        <input type="text" name="mail" placeholder="メールアドレス" v-model="formInput.mail">
        <textarea rows="5" v-model="formInput.comment" placeholder="メッセージ"></textarea>

        <div class="buttonBase" v-on:click="toSend">招待状を送信する</div>

        <Modal
            v-if="showModal"
            v-bind:mtypeIn='2'
            v-on:modal-ok-event    = "modalOk"
        >
            <p slot="header">@TJC message</p>
            <template slot="body">
                <p v-html="modalMsg" />
            </template>
        </Modal>

    </div>
</template>

<script>
import Modal     from '@/widgets/Modal'

export default {
    name : "InvitationComp",

    components :{
        Modal
    },

    methods:{
        async toSend(){
            if(this.formInput.mail == "")
            {
                this.modalMsg = "メールアドレスを入力してください"
                this.showModal = true
                return
            }

            this.formInput.url = process.env.RootUrl + "#/registration?invCode="

            await this.$store.dispatch('invitationUser', this.formInput)

            this.formInput.mail = ""
            this.formInput.comment = ""
        },

        modalOk(){
            this.showModal = false
        }

    },

    data: function () {
        return {
            formInput : {
                mail : "",
                comment : "",
                url : ""
            },
            showModal : false,
        }
    }
}
</script>

<style scoped>
    input{
        margin: 5px 0px 0px 10px;
    }

    textarea{
        margin: 20px 0px 0px 10px;
    }
</style>


