<template>
    <div>
        <h1>メンテナンス設定</h1>
        <div class="editBox" data-v-mts>
            <h3 data-v-mts>タイトル</h3>
            <input type="text" name="title" v-model="maintenanceInfo.title">

            <h3 data-v-mts>概要説明</h3>
            <textarea rows="5" name="desc" v-model="maintenanceInfo.desc"></textarea>
            
            <h3 data-v-mts>開始日時</h3>
            <input type="text" name="from" v-model="maintenanceInfo.from" placeholder="yyyy-MM-dd HH:mm:ss +0900">
            
            <h3 data-v-mts>終了日時</h3>
            <input type="text" name="to" v-model="maintenanceInfo.to" placeholder="yyyy-MM-dd HH:mm:ss +0900">
            <div class="buttonBase" v-on:click="maintenanceRegister">登録</div>
        </div>

    </div>
</template>

<script>
export default {
    name : "MaintenanceSetting",

    computed: {
        maintenanceInfo:{
            get()
            {
                return this.$store.getters.adminMaintenance
            },
            set(value)
            {
                this.$store.dispatch('setAdminMaintenanceFront', value)
                this.$store.dispatch('UpdateAdminMaintenance')
            }
        }
    },

    created()
    {
        this.$store.dispatch('GetAdminMaintenance')
        
        // 管理者チェック
        if(!this.$store.getters.userInfo.admin)
        {
            this.$router.push('/')
        }
    },

    methods:{
        maintenanceRegister()
        {
            this.$store.dispatch('UpdateAdminMaintenance')
        }
    },
    
    data: function () {
        return {
        }
    }

}
</script>

<style scoped>
    h2[data-v-mts] {
        font-size: 110%;
        font-weight: normal;
        color: #00a7ce;

        margin: 20px 0px 10px 0px;
    }

    h3[data-v-mts] {
        font-size: 100%;
        font-weight: normal;

        margin: 30px 0px 0px 0px;
    }
    
    .editBox[data-v-mts]{
        margin-bottom: 70px;
    }

    th[data-v-mts]{
        text-align: left;
        font-weight: normal;
        padding : 5px 0px;
    }

    td[data-v-mts] {
        padding:10px 10px;
    }

</style>


