<template>
    <section>
        <h1>{{ entry.title }}</h1>
        <div style="margin-bottom:30px;">
            <p class="cInfo">Author : <router-link v-bind:to="'/profile/' + entry.user">{{ entry.userId }}</router-link> , Date : {{ this.dat }}</p>
            
            <img v-bind:src="entry.imgUrl" v-if="entry.imgUrl != ''" width="100%" style="margin-top:10px">
            
            <div v-html="$sanitize(entry.body)" class="cBody"></div>
        </div>

        <router-link :to="{name : 'EditEntry'}"><div class="buttonBase" v-if="entry.editable">編集</div></router-link>
        <div class="buttonBase" v-on:click="delEntry" v-if="entry.editable">削除</div>
    </section>
</template>

<script>
export default {
    name : "Entry",
    data(){
        return {
        }
    },

    computed: {
        entry() {
            return this.$store.getters.entry;
        },
        dat(){
            let date = new Date(this.entry.dat)
            let y = date.getFullYear()
            let m = ("00" + (date.getMonth()+1)).slice(-2);
            let d = ("00" + date.getDate()).slice(-2)
            let datStr = y + "/" + m + "/" + d
            
            return datStr
        }
    },

    async mounted()
    {
        window.scrollTo(0,0);
        await this.$store.dispatch('initEntry', this.$route.params.entryNo)
    },
    watch :{
        async '$route' (to, from){
            await this.$store.dispatch('initEntry', this.$route.params.entryNo)
        }
    },

    methods:{
        async delEntry()
        {
            await this.$store.dispatch('delEntry', this.entry.no)
        },
    }


}
</script>

<style scoped>
    h1
    {
        font-size: 150%;
        margin: 30px 0px 10px 0px;
    }

    .cInfo{
        font-size:90%;
        margin: 5px 0px;
    }

    .cBody{
        margin-top:15px;
    }

</style>





