<template>
    <section>
        <div style="margin-bottom:50px;">
            <!-- <h1 data-v-regevent>イベント登録</h1> -->
            <h2 data-v-regevent>イベント名</h2>
            <input type="text" name="name" v-model="regData.name">

            <h2 data-v-regevent>概要</h2>
            <textarea rows="5" data-v-regevent name="desc" v-model="regData.desc"></textarea>

            <h2 data-v-regevent>期間</h2>
            <input type="datetime-local" class="fromToDate" data-v-regevent name="from" v-model="regData.from" v-if="!regData.allDay"> <input type="datetime-local" class="fromToDate" data-v-regevent name="to" v-model="regData.to" v-if="!regData.allDay">
            <input type="date" class="fromToDate" data-v-regevent name="from" v-model="regData.from" v-if="regData.allDay"> <input type="date" class="fromToDate" data-v-regevent name="to" v-model="regData.to" v-if="regData.allDay">
            <input type="checkbox" class="checkBoxInput" data-v-regevent name="allDay" v-model="regData.allDay" v-on:click="resetDate">
            <label>終日</label>

            <h2 data-v-regevent>場所</h2>
            <input type="text" name="posName" v-model="regData.pos.posName" placeholder="場所名・キーワードを入力してください" v-on:keyup.enter="searchPos">
            <div class="buttonBase" v-on:click="searchPos">検索</div>

            <SinglePosMap 
                ref="map"
                v-show="showMap"
            />
        </div>

        <p><input type="checkbox" v-model="regData.enableGuest" style="width:unset; height:unset;"><label>@TJC非会員ゲストにもイベントを公開する</label></p>

        <p><input type="checkbox" v-if="!update" v-model="regData.twitter" style="width:unset; height:unset;"><label v-if="!update"><a href="https://twitter.com/tjc_ob_app" target="_blank">@TJC twitter</a>へ投稿する</label></p>
        <div class="buttonBase" v-on:click="register" v-if="!update" style="margin-top:25px;">登録</div>
        <div class="buttonBase" v-on:click="updateEvent" v-if="update">更新</div>

        <Modal
            v-if="showModal"
            v-bind:mtypeIn='2'
            v-on:modal-ok-event    = "modalOk"
        >
            <p slot="header">@TJC message</p>
            <template slot="body">
                <p v-html="modalMsg" />
            </template>
        </Modal>

    </section>
</template>

<script>
import  SinglePosMap from '@/widgets/SinglePosMap'
import Modal         from '@/widgets/Modal'

export default {
    name : "RegEvent",
    
    components :{
        SinglePosMap,
        Modal
    },

    data(){
        return{
            regData : {
                no   : "",
                name : "",
                desc : "",
                pos : 
                {
                    posName : "",
                    lat : null,
                    lng : null
                },
                from : null,
                to : null,
                allDay : false,
                twitter : false,
                enableGuest : false
            },
            showMap : false,

            modalMsg : "",
            showModal : false,

            update : false
        }
    },

    watch :{
        // 更新時の初期化処理
        async '$route' (to, from){
            if(to.name == "UpdateEvent" && to.params.eventNo != null)
            {
                this.initData(to.params.eventNo)
            }
        }
    },

    created:async function(){
        if(this.$route.name == "UpdateEvent" && this.$route.params.eventNo != null)
        {
            this.initData(this.$route.params.eventNo)
        }
    },

    methods :{
        async initData(eventNo)
        {
            window.scrollTo(0,0)
            await this.$store.dispatch('initEvent', eventNo)

            // populate
            let data =this.$store.getters.event;
            this.regData.no   = data.no
            this.regData.name = data.name
            this.regData.desc = data.desc
            this.regData.allDay = data.allDay
            this.regData.from   = data.from
            this.regData.to     = data.to
            this.regData.pos.posName = data.pos.posName
            this.regData.pos.lat     = data.pos.lat
            this.regData.pos.lng     = data.pos.lng
            this.update = data.dispControl.update
            this.regData.enableGuest = data.enableGuest

            // 更新不可イベント
            if(!this.update)
            {
                this.$router.push({name:'EventList'})
            }

            this.$store.dispatch('setPosSingleMap', {lat:this.regData.pos.lat, lng:this.regData.pos.lng})

            // 地図表示フラグ
            if(this.regData.pos.posName == null || this.regData.pos.posName == "")
            {
                this.showMap = false
            }
            else
            {
                this.showMap = true
            }
            
            this.$refs.map.initMap()
        },

        resetDate()
        {
            this.regData.from = null
            this.regData.to   = null
        },

        async searchPos()
        {
            console.log("search")
            await this.$store.dispatch('searchPos', this.regData.pos.posName)
            this.showMap = true
            this.$refs.map.initMap()
            this.regData.pos.lat = this.$store.getters.posSingleMap.lat
            this.regData.pos.lng = this.$store.getters.posSingleMap.lng
        },

        register()
        {
            if(!this.validate())
            {
                return
            }

            this.$store.dispatch('regEvent', this.regData)
        },

        updateEvent()
        {
            if(!this.validate())
            {
                return
            }

            this.$store.dispatch('editEvent', this.regData)
        },

        validate()
        {
            // イベント名必須
            if(this.regData.name == "")
            {
                this.modalMsg = "イベント名を入力してください"
                this.showModal = true
                return false
            }
            // 場所
            // if(this.regData.pos.lat == null || this.regData.pos.lng == null || this.regData.pos.lat == "" || this.regData.pos.lng == "")
            if(
                // 場所名が設定されている場合
                (this.regData.pos.posName != null && this.regData.pos.posName != "")
                &&
                // 座標情報が不正
                (this.regData.pos.lat == null || this.regData.pos.lng == null || this.regData.pos.lat == "" || this.regData.pos.lng == "")
            )
            {
                this.modalMsg = "場所の指定が不正です"
                this.showModal = true
                return false
            }
            // 期間
            if(this.regData.from == null || this.regData.to == null || this.regData.from == "" || this.regData.to == "")
            {
                this.modalMsg = "期間の指定が不正です"
                this.showModal = true
                return false
            }

            return true

        },

        modalOk(){
            this.showModal = false
        }
    }
}
</script>

<style scoped>
    h1[data-v-regevent] {
        font-size: 150%;
    }

    h2[data-v-regevent] {
        font-size: 110%;
        font-weight: normal;
        /* color: #00a7ce; */

        margin: 20px 0px 0px 0px;
    }

    h3[data-v-regevent] {
        font-size: 100%;
        font-weight: normal;

        margin: 5px 0px;
    }

    p[data-v-regevent]
    {
        margin-left:10px;
    }

    ul li[data-v-regevent] {
        font-size: 100%;
    }

    ul[data-v-regevent] {
        margin: 0px 0px 0px 5px;
    }

    ul li[data-v-regevent]:before {
        content: "- ";
        color: #737373;
        font-weight: bold;

        margin: 5px 0px;
    }

    .comment[data-v-regevent]
    {
        background-color: #f1f1f1;
        border: solid 1px #d5d5d5;
        border-radius: 5px;
        padding : 10px;
        margin : 10px 0px;
    }
    p.commentBody[data-v-regevent]
    {
        margin-left:0px;
    }

    .commentFooter[data-v-regevent]
    {
        font-size: 90%;
        text-align: right;
    }

    input.fromToDate[data-v-regevent]
    {
        width : 250px;
    }

    .checkBoxInput[data-v-regevent]{
        width: unset;
        height: unset;
    }

</style>




