<template>
    <article>
        <p>会員登録 完了しました。</p>
        <p>ログインページからログインしてください。</p>

        <div class="buttonBase" v-on:click="toLogin" data-v-compReg>ログインページへ</div>
    </article>
</template>

<script>
export default {
    
    methods:{
        toLogin(){
            this.$router.push({name:'Portal'})
        }
    }
}
</script>


<style>
    .buttonBase[data-v-compReg]{
        margin-top:30px;
    }
</style>



