<template>
    <article>
        <table data-v-pwUpdate>
            <tr>
                <td>現在のパスワード</td>
                <td><input type="password" name="pw" placeholder="半角英数字記号 必須" v-model="currentPW" data-v-pwUpdate></td>
            </tr>
            <tr>
                <td>変更後パスワード</td>
                <td><input type="password" name="pw" placeholder="半角英数字記号 必須" v-model="updatedPW" data-v-pwUpdate></td>
            </tr>
            <tr>
                <td>変更後パスワード(再確認)</td>
                <td><input type="password" name="pw-re" placeholder="半角英数字記号 必須" v-model="updatedPWRe" v-on:keyup.enter="toRegister" data-v-pwUpdate></td>
            </tr>
        </table>
        <div class="info" data-v-pwUpdate>
            <p>※ 現在パスワードと同じパスワードを変更後パスワードに設定できません。</p>
        </div>

        <div class="buttonBase" v-on:click="toRegister">変更</div>

        <Modal
            v-show="showModal"
            v-bind:mtypeIn='2'
            v-on:modal-ok-event    = "modalOk"
        >
            <p slot="header">@TJC message</p>
            <template slot="body">
                <p v-html="modalMsg" />
            </template>
        </Modal>

    </article>
</template>

<script>
import Modal from '@/widgets/Modal'

export default {
    name: "UpdatePassWord",

    components :{
        Modal
    },

    data(){
        return{
            currentPW   : "",
            updatedPW   : "",
            updatedPWRe : "",

            modalMsg : "",

            showModal : false
        }
    },

    methods:{
        //  パスワード変更
        toRegister(){
            // 必須チェック
            if(this.currentPW == "" || this.updatedPW == "" || this.updatedPWRe == "")
            {
                this.currentPW   = ""
                this.updatedPW   = ""
                this.updatedPWRe = ""

                this.modalMsg   = "現在パスワード、変更後パスワード、変更後パスワード(再確認)を入力してください。"
                this.showModal  = true
                return
            }

            // 変更後パスワードチェック
            else if(this.updatedPW != this.updatedPWRe)
            {
                this.currentPW   = ""
                this.updatedPW   = ""
                this.updatedPWRe = ""

                this.modalMsg   = "変更後パスワードと変更後パスワード(再確認)が一致しません。"
                this.showModal  = true
                return
            }

            // 現在パスワードと変更後パスワードチェック
            else if(this.currentPW == this.updatedPW)
            {
                this.currentPW   = ""
                this.updatedPW   = ""
                this.updatedPWRe = ""

                this.modalMsg   = "現在パスワードと同じパスワードには変更できません。"
                this.showModal  = true
                return
            }

            // パスワード変更
            this.$store.dispatch('updatePW', {currentPW:this.currentPW, updatedPW:this.updatedPW, updatedPWRe:this.updatedPWRe})
        },

        modalOk(){
            this.showModal = false
        }
    }
}
</script>

<style scoped>
    table[data-v-pwUpdate]{
        width: 100%;
        margin: 0 auto;
        /* margin-bottom: 30px; */
    }
    .info[data-v-pwUpdate]{
        margin: 30px 0px;
        font-size: 80%;
    }
</style>



