<template>
    <section>
        <h1>パスワード再設定</h1>
        <p>会員登録されているメールアドレスと新しいパスワードを入力し、「再設定」をクリックしてください。</p>

        <input type="text" name="mail" placeholder="登録メールアドレス" v-model="input.email" style="margin:10px 0px;">
        <input type="password" name="pw" placeholder="パスワード" v-model="input.updatedPW" style="margin:10px 0px;">
        <input type="password" name="pw" placeholder="パスワード（再確認）" v-model="input.updatedPWRe" style="margin:10px 0px;">

        <div class="buttonBase" v-on:click="toSent">再設定</div>
    </section>
</template>

<script>
export default {
    name : "ResetPassword",
    created: function(){
        // hide toggre
        this.$store.dispatch('setToggreState', false)

        this.input.key = this.$route.query.key
        
    },

    data(){
        return{
            input : {
                email:null,
                key  :null,
                updatedPW   : null,
                updatedPWRe : null
            }
        }
    },

    methods:{
        toSent(){
            // let url = location.hostname
            this.$store.dispatch('resetPassword', this.input)
        }
    }
}
</script>


