<template>
    <div>
        <h2>タイトル</h2>
        <input type="text" name="title" v-model="entry.title">

        <h2>本文</h2>
        <textarea rows="5" name="body" v-model="entry.body"></textarea>

        <h2 v-if="entry.imgUrl == ''">画像添付</h2>
        <h2 v-if="entry.imgUrl != ''">添付画像変更</h2>
        <input type="file" name="image" v-on:change="selectFile">

        <p v-if="entry.no == null" style="margin-top:30px;"><input type="checkbox" v-model="entry.twitter" style="width:unset; height:unset;"><label><a href="https://twitter.com/tjc_ob_app" target="_blank">@TJC twitter</a>へ投稿する <br>(投稿内容 :『新しいエントリが登録しました。「{{ entry.title }}」』 )</label></p>
    </div>
</template>

<script>
export default {
    name : "EntryForm",
    data(){
        return {
        }
    },

    computed: {
        entry:{
            get()
            {
                return this.$store.getters.entry
            },
            set(value)
            {
                this.$store.dispatch('setEntry', value)
            }
        },
        userInfo:{
            get()
            {
                return this.$store.getters.userInfo
            }
        }
    },

    methods:{
        selectFile(e){
            console.log("in select")
            e.preventDefault();
            let files = e.target.files;
            this.entry.imgFile = files[0];
        }
    }
}
</script>

<style scoped>
    h1 {
        font-size: 150%;
    }

    h2 {
        font-size: 110%;
        font-weight: normal;
        /* color: #00a7ce; */

        margin: 20px 0px 0px 0px;
    }

    h3 {
        font-size: 100%;
        font-weight: normal;

        margin: 5px 0px;
    }

    p
    {
        margin-left:10px;
    }

    ul li {
        font-size: 100%;
    }

    ul {
        margin: 0px 0px 0px 5px;
    }

    ul li:before {
        content: "- ";
        color: #737373;
        font-weight: bold;

        margin: 5px 0px;
    }

    .comment
    {
        background-color: #f1f1f1;
        border: solid 1px #d5d5d5;
        border-radius: 5px;
        padding : 10px;
        margin : 10px 0px;
    }
    p.commentBody
    {
        margin-left:0px;
    }

    .commentFooter
    {
        font-size: 90%;
        text-align: right;
    }

    input.fromToDate
    {
        width : 250px;
    }

    .checkBoxInput{
        width: unset;
        height: unset;
    }
</style>


