<template>
  <article>
    <Profile/>
    <div class="buttonBase" v-on:click="toEdit">プロフィール編集</div>
    <div class="buttonBase" v-on:click="toPassword">パスワード変更</div>
    <div class="buttonBase" v-on:click="toInvitation">ユーザーを招待</div>
  </article>
</template>

<script>
import Profile from "@/components/Profile";
export default {
  name: "MyPage",

  components: {
    Profile
  },

  methods: {
    toEdit(){
        this.$router.push({name:'EditProfile'})
    },

    toPassword(){
      this.$router.push({name:'UpdatePassword'})
    },

    toInvitation()
    {
      this.$router.push({name:'Invitation'})
    }
  }
};
</script>

<style scoped>
</style>



