<template>
    <section>
        <h1 data-v-event>{{ event.name }}</h1>

        <SinglePosMap 
            ref="map"
        />

        <h2 data-v-event>概要</h2>
        <p data-v-event>{{ event.desc }}</p>

        <h2 data-v-event>場所</h2>
        <p data-v-event>{{ event.pos.posName }}</p>
        <h2 data-v-event>日時</h2>
        <p data-v-event>{{ event.time }}</p>

        <template v-if="event.enableGuest">
        <h2 data-v-event>公開イベント</h2>
        <p data-v-event>公開用URL：{{ openUrl }}</p>
        </template>

        <h2 data-v-event>オーナー</h2>
        <p data-v-event><router-link v-bind:to="'/profile/' + event.owner.userNo">{{ event.owner.loginId }}</router-link></p>
        <h2 data-v-event>参加者</h2>
        <ul data-v-event>
            <li data-v-event v-for="(guest, index) in event.guests" :key="index">
                <router-link v-bind:to="'/profile/' + guest.userNo" v-if="guest.userNo != null">{{guest.loginId}}</router-link>
                <template v-if="guest.userNo == null">{{guest.loginId}}</template>
            </li>
        </ul>
        <div class="buttonBase" v-on:click="add" v-if="event.dispControl.addGuests">参加する</div>
        <div class="buttonBase" v-on:click="cancel" v-if="event.dispControl.removeGuests">参加を取消</div>

        <div style="margin:40px 0px;">
            <router-link v-bind:to="'/events/update/' + event.no"><div class="buttonBase" v-if="event.dispControl.update">イベント更新</div></router-link>
            <div class="buttonBase" v-on:click="cancelEvent" v-if="event.dispControl.delEvent">イベントを取消</div>
        </div>
            
        <h2 data-v-event>コメント</h2>
        <input type="text" placeholder="コメントを入力してください" v-model="inputComment">
        <div class="buttonBase" v-on:click="addComment">コメント追加</div>
        <div class="comment" data-v-event v-for="(comment, index) in event.comments" :key="index">
            <p class="commentBody" data-v-event>{{ comment.body }}</p>
            <p class="commentFooter" data-v-event>{{ comment.footer }}</p>
        </div>

        <Modal
            v-if="showModal"
            v-bind:mtypeIn='2'
            v-on:modal-ok-event    = "modalOk"
        >
            <p slot="header">@TJC message</p>
            <template slot="body">
                <p v-html="modalMsg" />
            </template>
        </Modal>


    </section>
</template>

<script>
import  SinglePosMap from '@/widgets/SinglePosMap'
import Modal         from '@/widgets/Modal'
export default {
    name : "Event",
    
    components :{
        SinglePosMap,
        Modal
    },

    computed: {
        event() {
            return this.$store.getters.event;
        }
    },

     data(){
        return{
            inputComment : "",
            modalMsg : "",
            showModal : false,
            openUrl : ""
        }
     },

    created: async function()
    {
        window.scrollTo(0,0)
        this.$store.dispatch('setToggreState', true)
        await this.$store.dispatch('initEvent', this.$route.params.eventNo)
        this.$store.dispatch('setPosSingleMap', {lat:this.event.pos.lat, lng:this.event.pos.lng})
        this.$refs.map.initMap()

        this.openUrl = process.env.RootUrl + "#/guest/events/detail/" + this.event.no

    },
    watch :{
        async '$route' (to, from){
            await this.$store.dispatch('initEvent', to.params.eventNo)
            this.$store.dispatch('setToggreState', true)
            this.$store.dispatch('setPosSingleMap', {lat:this.event.pos.lat, lng:this.event.pos.lng})
            this.$refs.map.initMap()

            this.openUrl = process.env.RootUrl + "#/guest/events/detail/" + this.event.no
        }
    },

    methods :{
        async add()
        {
            await this.$store.dispatch('addGuest', this.event.no)
            this.$store.dispatch('initEvent', this.$route.params.eventNo)
        },

        async cancel()
        {
            await this.$store.dispatch('removeGuest', this.event.no)
            this.$store.dispatch('initEvent', this.$route.params.eventNo)
        },
        async cancelEvent()
        {
            await this.$store.dispatch('cancelEvent', this.event.no)
        },
        async addComment()
        {
            if(this.inputComment == "")
            {
                this.modalMsg = "コメントを入力してください"
                this.showModal = true
                return
            }
            await this.$store.dispatch('addComment', {eventNo : this.event.no, comment : this.inputComment})
            this.$store.dispatch('initEvent', this.$route.params.eventNo)
            this.inputComment = ""
        },

        modalOk(){
            this.showModal = false
        }
    }
}
</script>

<style scoped>
    h1[data-v-event] {
        font-size: 150%;
    }

    h2[data-v-event] {
        font-size: 110%;
        font-weight: normal;
        /* color: #00a7ce; */

        margin: 20px 0px 10px 0px;
    }

    h3[data-v-event] {
        font-size: 100%;
        font-weight: normal;

        margin: 5px 0px;
    }

    p[data-v-event]
    {
        margin-left:10px;
    }

    ul li[data-v-event] {
        font-size: 100%;
    }

    ul[data-v-event] {
        font-size: 100%;
        margin: 0px 0px 20px 5px;
    }

    ul li[data-v-event]:before {
        content: "- ";
        color: #737373;
        font-weight: bold;

        margin: 5px 0px;
    }

    .comment[data-v-event]
    {
        background-color: #f1f1f1;
        border: solid 1px #d5d5d5;
        border-radius: 5px;
        padding : 10px;
        margin : 10px 0px;
    }
    p.commentBody[data-v-event]
    {
        margin-left:0px;
    }

    .commentFooter[data-v-event]
    {
        font-size: 90%;
        text-align: right;
    }

</style>




