<template>
    <div>
        <h1>Information</h1>
        <h2 data-v-adifdtl>タイトル</h2>
        <input type="text" name="title" v-model="info.title">
        <h2 data-v-adifdtl>内容</h2>
        <textarea rows="5" name="body" v-model="info.body"></textarea>
        <h2 data-v-adifdtl>掲載開始日時</h2>
        <input type="datetime-local" name="from" v-model="info.from" placeholder="yyyy-MM-dd HH:mm:ss +0900">
        <h2 data-v-adifdtl>掲載終了日時</h2>
        <input type="datetime-local" name="to" v-model="info.to" placeholder="yyyy-MM-dd HH:mm:ss +0900">

        <div class="buttonBase" v-on:click="registerInfo" v-if="!update" style="margin-top:25px;">登録</div>
        <div class="buttonBase" v-on:click="updateInfo" v-if="update" style="margin-top:25px;">更新</div>
        <div class="buttonBase" v-on:click="deleteInfo" v-if="update" style="margin-top:25px;">削除</div>
    </div>
</template>

<script>
export default {
    name : "AdminInfoDtl",
    computed: {
        info:{
            get()
            {
                return this.$store.getters.adminInfo
            },
            set(value)
            {
                this.$store.dispatch('setAdminInfo', value)
            }
        }
    },

    created()
    {
        if(this.$route.name == "AdminInfoDtl" && this.$route.params.infoId != null)
        {
            this.$store.dispatch('GetAdminInfo', this.$route.params.infoId)
            this.update = true
        }
        // 管理者チェック
        if(!this.$store.getters.userInfo.admin)
        {
            this.$router.push('/')
        }
    },

    methods:{
        updateInfo()
        {
            this.$store.dispatch('UpdateAdminInfo', this.info)
        },

        registerInfo()
        {
            this.$store.dispatch('RegAdminInfo', this.info)
        },

        deleteInfo()
        {
            this.$store.dispatch('DelAdminInfo', this.info.id)
        }
    },
    
    data: function () {
        return {
            update : false
        }
    }
}
</script>

<style scoped>
    h2[data-v-adifdtl] {
        font-size: 110%;
        font-weight: normal;
        /* color: #00a7ce; */

        margin: 20px 0px 0px 0px;
    }
</style>


