<template>
    <section>
        <h1>新規投稿</h1>
        <EntryForm />
        <div class="buttonBase" v-on:click="register" style="margin-top:30px;">投稿</div>

        <Modal
            v-if="showModal"
            v-bind:mtypeIn='2'
            v-on:modal-ok-event = "showModal = false"
        >
            <p slot="header">@TJC message</p>
            <template slot="body">
                <p v-html="modalMsg" />
            </template>
        </Modal>

    </section>
</template>

<script>
import EntryForm from '@/components/EntryForm'
import Modal     from '@/widgets/Modal'

export default {
    components :{
        EntryForm,
        Modal
    },

    name : "RegEntry",
    data(){
        return {
            modalMsg : "",
            showModal : false
        }
    },

    computed: {
        entry:{
            get()
            {
                return this.$store.getters.entry
            },
            set(value)
            {
                this.$store.dispatch('setEntry', value)
            }
        }
    },

    mounted()
    {
        this.entry.no     = null
        this.entry.title  = ""
        this.entry.body   = ""
        this.entry.imgUrl = ""
    },

    methods:{
        register()
        {
            if(!this.validate())
            {
                return
            }

            this.$store.dispatch('regEntry', this.entry)

        },

        validate()
        {
            // タイトル必須
            if(this.entry.title == "")
            {
                this.modalMsg = "タイトルを入力してください"
                this.showModal = true
                return false
            }
            // 本文必須
            if(this.entry.body == "")
            {
                this.modalMsg = "本文を入力してください"
                this.showModal = true
                return false
            }

            return true
        },

    }
}
</script>

<style scoped>

</style>


