<template>
    <section>
        <h1 data-v-event>{{ event.name }}</h1>

        <SinglePosMap 
            ref="map"
        />

        <h2 data-v-event>Info</h2>
        <p data-v-event>このページはゲスト用です。@TJC会員登録済の方は会員用ページへお進みください。</p>
        <router-link v-bind:to="'/events/detail/' + event.no"><div class="buttonBase">会員用ページ</div></router-link>

        <h2 data-v-event>概要</h2>
        <p data-v-event>{{ event.desc }}</p>

        <h2 data-v-event>場所</h2>
        <p data-v-event>{{ event.pos.posName }}</p>
        <h2 data-v-event>日時</h2>
        <p data-v-event>{{ event.time }}</p>
        <h2 data-v-event>オーナー</h2>
        <p data-v-event>{{ event.owner.loginId }}</p>
        <h2 data-v-event>参加者</h2>
        <ul data-v-event>
            <li data-v-event v-for="(guest, index) in event.guests" :key="index">
                {{guest.loginId}}
            </li>
        </ul>
        <input type="text" placeholder="名前を入力してください" v-model="inputGuestName" v-if="isAdd">
        <div class="buttonBase" v-on:click="add" v-if="isAdd">参加する</div>
        <div class="buttonBase" v-on:click="cancel" v-if="!isAdd">参加を取消</div>

            
        <h2 data-v-event>コメント</h2>
        <input type="text" placeholder="名前を入力してください" v-model="inputName">
        <input type="text" placeholder="コメントを入力してください" v-model="inputComment">
        <div class="buttonBase" v-on:click="addComment">コメント追加</div>
        <div class="comment" data-v-event v-for="(comment, index) in event.comments" :key="index">
            <p class="commentBody" data-v-event>{{ comment.body }}</p>
            <p class="commentFooter" data-v-event>{{ comment.footer }}</p>
        </div>

        <Modal
            v-if="showModal"
            v-bind:mtypeIn='2'
            v-on:modal-ok-event    = "modalOk"
        >
            <p slot="header">@TJC message</p>
            <template slot="body">
                <p v-html="modalMsg" />
            </template>
        </Modal>


    </section>
</template>

<script>
import  SinglePosMap from '@/widgets/SinglePosMap'
import Modal         from '@/widgets/Modal'
export default {
    name : "GuestEvent",
    
    components :{
        SinglePosMap,
        Modal
    },

    computed: {
        event() {
            return this.$store.getters.event;
        }
    },

     data(){
        return{
            inputGuestName : "",
            inputName : "",
            inputComment : "",
            modalMsg : "",
            showModal : false,
            gtoken : null,
            isAdd : true
        }
     },

    created: async function()
    {
        window.scrollTo(0,0)
        // hide toggre
        this.$store.dispatch('setToggreState', false)

        await this.$store.dispatch('initGuestEvent', this.$route.params.eventNo)
        this.$store.dispatch('setPosSingleMap', {lat:this.event.pos.lat, lng:this.event.pos.lng})
        this.$refs.map.initMap()

        // get guest token
        let key = "E" + this.event.no
        this.gtoken = localStorage.getItem(key)
        if(this.gtoken != null)
        {
            this.isAdd = false
        }
    },
    watch :{
        async '$route' (to, from){
            // hide toggre
            this.$store.dispatch('setToggreState', false)

            await this.$store.dispatch('initGuestEvent', to.params.eventNo)
            this.$store.dispatch('setPosSingleMap', {lat:this.event.pos.lat, lng:this.event.pos.lng})
            this.$refs.map.initMap()

            // get guest token
            let key = "E" + this.event.no
            this.gtoken = localStorage.getItem(key)
            if(this.gtoken != null)
            {
                this.isAdd = false
            }
        }
    },

    methods :{
        async add()
        {
            if(this.inputGuestName == "")
            {
                this.modalMsg = "名前が未入力です。"
                this.showModal = true
                return
            }
            let token = new Date().getTime().toString(16)  + Math.floor(100000*Math.random()).toString(16)

            await this.$store.dispatch('addOtGuest', {eventNo : this.event.no, name: this.inputGuestName, gtoken:token})
            this.$store.dispatch('initGuestEvent', this.$route.params.eventNo)

            // get guest token
            let key = "E" + this.event.no
            this.gtoken = localStorage.getItem(key)
            if(this.gtoken != null)
            {
                this.isAdd = false
            }
        },

        async cancel()
        {
            await this.$store.dispatch('removeOtGuest', {eventNo : this.event.no, gtoken:this.gtoken})
            this.$store.dispatch('initGuestEvent', this.$route.params.eventNo)
            
            this.isAdd = true
            // get guest token
            let key = "E" + this.event.no
            this.gtoken = localStorage.getItem(key)
            if(this.gtoken != null)
            {
                this.isAdd = false
            }
        },
        async addComment()
        {
            if(this.inputComment == "" || this.inputName == "")
            {
                this.modalMsg = "名前、またはコメントが未入力です。"
                this.showModal = true
                return
            }
            await this.$store.dispatch('addGuestComment', {eventNo : this.event.no, comment : this.inputComment, name : this.inputName})
            this.$store.dispatch('initGuestEvent', this.$route.params.eventNo)
            this.inputComment = ""
        },

        modalOk(){
            this.showModal = false
        }
    }
}
</script>

<style scoped>
    h1[data-v-event] {
        font-size: 150%;
    }

    h2[data-v-event] {
        font-size: 110%;
        font-weight: normal;
        /* color: #00a7ce; */

        margin: 20px 0px 10px 0px;
    }

    h3[data-v-event] {
        font-size: 100%;
        font-weight: normal;

        margin: 5px 0px;
    }

    p[data-v-event]
    {
        margin-left:10px;
    }

    ul li[data-v-event] {
        font-size: 100%;
    }

    ul[data-v-event] {
        font-size: 100%;
        margin: 0px 0px 20px 5px;
    }

    ul li[data-v-event]:before {
        content: "- ";
        color: #737373;
        font-weight: bold;

        margin: 5px 0px;
    }

    .comment[data-v-event]
    {
        background-color: #f1f1f1;
        border: solid 1px #d5d5d5;
        border-radius: 5px;
        padding : 10px;
        margin : 10px 0px;
    }
    p.commentBody[data-v-event]
    {
        margin-left:0px;
    }

    .commentFooter[data-v-event]
    {
        font-size: 90%;
        text-align: right;
    }

</style>




