<template>
    <div id='map' data-v-singleposmap
        v-show="showMap"
    >
        MAP
    </div>
</template>

<script>
export default {
    name : "SinglePosMap",

    data(){
        return{
            showMap : true
        }
    },

    computed:{
        pos:{
            get(){
              return this.$store.getters.posSingleMap  
            },
            set(value){
                this.$store.dispatch('setPosSingleMap', value)
            }
        }
    },

    methods:{
        initMap(){
            var map = new google.maps.Map(document.getElementById('map'), {
                zoom: 15,
                center: this.pos
            })
            var maker =  new google.maps.Marker({
                position: this.pos,
                map: map
            });
        }
    },

    mounted() {
        this.initMap();
    },

    updated()
    {
        this.initMap();
    },

    watch :{
        '$route' (to, from){
            this.initMap()
        }
    },
}
</script>

<style scoped>
    #map[data-v-singleposmap]
    {
        height: 300px;
        width: 100%;
    }
</style>




