<template>
    <div>
        <h1>Admin</h1>
        <h2 data-v-admn>Portal Information</h2>

        <table width="100%">
            <tr>
                <th width="30%" data-v-admn>タイトル</th><th width="30%" data-v-admn>From</th><th width="30%" data-v-admn>To</th>
            </tr>
            <tr v-for="(info, index) in infos" :key="index">
                <td data-v-admn><router-link v-bind:to="'/admin/info/' + info.id">{{info.title}}</router-link></td>
                <td data-v-admn>{{info.from}}</td>
                <td data-v-admn>{{info.to}}</td>
            </tr>
        </table>

        <router-link :to="{name:'RegAdminInfo'}"><div class="buttonBase" style="margin-top:25px;">Information登録</div></router-link>

    </div>
</template>

<script>
export default {
    name : "AdminInfo",

    computed: {
        infos:{
            get()
            {
                return this.$store.getters.adminInfos
            }
        }
    },

    created()
    {
        this.$store.dispatch('GetAdminInfos', this.$route.params.infoId)

        // 管理者チェック
        if(!this.$store.getters.userInfo.admin)
        {
            this.$router.push('/')
        }
    },

    methods:{

    },
    
    data: function () {
        return {
        }
    }

}
</script>

<style scoped>
    h2[data-v-admn] {
        font-size: 110%;
        font-weight: normal;
        color: #00a7ce;

        margin: 20px 0px 10px 0px;
    }

    th[data-v-admn]{
        text-align: left;
        font-weight: normal;
        padding : 5px 0px;
    }

    td[data-v-admn] {
        padding:10px 10px;
    }

</style>


