<template>
  <div id="app">
    <Header />
    <!-- <img src="./assets/logo.png"> -->
    <router-view/>

    <transition name="sidef">
      <SideMenu v-if=sidebarState />
    </transition>

    <Modal
        v-show="showModal"
        v-bind:mtypeIn='2'
        v-on:modal-ok-event = "globalModalOk"
    >
        <p slot="header">@TJC message</p>
        <template slot="body">
            <!-- <p>モーダルメッセージ</p> -->
            <p v-html="modalMsg" />
        </template>
    </Modal>

    <Loading v-if="loadingState" />

  </div>
</template>

<script>
import Header   from '@/components/Header'
import SideMenu from '@/components/SideMenu'
import Modal    from '@/widgets/Modal'
import Loading  from '@/widgets/Loading'



export default {
  name: 'App',

  components: {
      Header,
      SideMenu,
      Modal,
      Loading
  },

  computed: {
    showModal()
    {
      return this.$store.getters.showModal
    },
    modalMsg()
    {
      return this.$store.getters.modalMsg
    },
    loadingState()
    {
      return this.$store.getters.loading
    },
    sidebarState()
    {
      return this.$store.getters.showSideBar
    }
  },

  methods:{
    globalModalOk(){
      this.$store.dispatch('setShowModal', false)
      // location.reload()
    }
  },

  mounted(){
    // google map API 読み込み
    var script = document.createElement('script');
    script.src = "https://maps.googleapis.com/maps/api/js?key=" + process.env.MAP_API_KEY;
    document.body.appendChild(script);
  }

}
</script>

<style>
@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url(https://fonts.googleapis.com/earlyaccess/notosansjapanese.css);
@import url(https://fonts.googleapis.com/css?family=Carme);
@import url(https://fonts.googleapis.com/css?family=Didact+Gothic);


/*  --  Start of Global setting --  */
    body{
        font-family: 'Open Sans','Rounded Mplus 1c', Segoe UI, "メイリオ", Meiryo, sans-serif;
        font-weight: 300;
        color:#737373;
        background-color:#ffffff;
        margin: 0 auto;
        
        max-height: 100%;
        -webkit-text-size-adjust: 100%;
    }
    p {
        max-height: 100%;
        margin : 5px 0px;
    }

    ul{
        list-style-type: none;
        margin-left:0px;
        padding-left:0px;
        font-size : 90%;
    }
    ul li:before{
        content: '〇 ';
        color: #ffc000;
        font-weight: bold;
    }

    li{
        margin: 5px 0px;
    }

    a {
        color : #00a7ce;
        text-decoration: none;
        border-bottom: 1px solid #00a7ce;
        transition-property: all;
        transition: 0.2s linear;
    }

    a:hover{
        color: #dcdcdc;
        border-bottom: 1px solid #dcdcdc;
    }

    input {
        background: #fafafa;
        border: none;
        border-bottom: 2px solid #e9e9e9;
        color: #666;
        font-family: 'Open Sans', sans-serif;
        font-size: 1em;
        height: 50px;
        transition: border-color 0.3s;
        width: 100%;
    }
    input:focus {
        border-bottom: 2px solid #c0c0c0;
        outline: none;
    }

    textarea{
      background: #fafafa;
      border: none;
      border-bottom: 2px solid #e9e9e9;
      color: #666;
      font-family: 'Open Sans', sans-serif;
      font-size: 1em;
      transition: border-color 0.3s;
      width: 100%;
    }
    textarea:focus{
      border-bottom: 2px solid #c0c0c0;
      outline: none;
    }

/*  --  End of Global setting --  */

/* -- Start of Global parts setting -- */
    .buttonBase {
      color: #099acb;
      border: 1px #099acb solid;
      border-radius: 5px;
      cursor: pointer;
      font-size: 1em;
      letter-spacing: 0.05em;
      text-align: center;
      text-transform: uppercase;
      transition: background 0.3s ease-in-out;
      /* width: 100%; */
      padding : 5px;
      margin : 10px 0px;
    }

    .buttonBase:hover {
      color: #BBDEFB;
      border: 1px #BBDEFB solid;
    }
/* -- End of Global parts setting -- */

/* -- Start of main content section */
    section {
        width: 90%;
        max-width: 800px;
        margin: 0 auto;
        margin-top: 30px;
        margin-bottom: 30px;
        display: block;
    }

    article
    {
      margin: 0px;
      padding: 0px;
    }

/* -- End of main content section */

/* -- Start of Map Maker Global settings --*/ 
    .mapInfo
    {
      width:300px;
      font-size:120%;
    }

    .markerTitle{
        color : #00a7ce;
        font-size : 150%;
        font-weight: bolder;
    }
/* -- End of Map Maker Global settings --*/ 

    .sidef-enter-active, .sidef-leave-active{
        transition: opacity 0.5s, transform 0.5s;
    }
    .sidef-enter{
        opacity: 0;
        transform : translatex(-250px);
    }
    .sidef-leave-to{
        opacity: 0;
        transform : translatex(-250px);
    }

/*  --  Start of navigation setting -- */
    nav {
        border-bottom : solid 2px #d9d9d9;
        height: 40px;
    }

    nav .btnBox {
        width: 100%;
        max-width : 456px;
        margin: 0 auto;
    }

    .btn {
        width: 32.7%;
        max-width: 150px;
        height:32px;
        padding-top: 8px;

        float: left;
        display: block;
        
        text-align: center;
        font-size: 100%;
        font-weight: 200;
    }

    a.btn{
      color:#737373;
      border-bottom: none;
    }
  
    .btn:hover {
        border-bottom: solid 3px #c1e5ff;
    }

/*  --  End of navigation setting -- */

/* #app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
} */
</style>



