<template>
    <section>
        <h1 data-v-members>Member List</h1>
        <h2 v-for="(member, index) in members" :key="index" data-v-members>
            <router-link v-bind:to="'/profile/' + member.userNo">{{ member.loginId }}</router-link>
            <h3 data-v-members>{{member.comment}}</h3>
        </h2>

    </section>
</template>

<script>
export default {
    name : "Members",
    computed: {
        members() {
        return this.$store.getters.members;
        }
    },

    mounted()
    {
        this.$store.dispatch('initMemberlist')
    },
    
    data: function () {
        return {
        }
    }
}   
</script>

<style scoped>
    h1[data-v-members] {
        font-size: 150%;
    }

    h2[data-v-members] {
        font-size: 120%;
        font-weight: normal;
        margin: 15px 0px 3px 10px;
    }

    h3[data-v-members] {
        font-size: 80%;
        font-weight: normal;
        margin: 5px 0px 0px 10px;
    }
</style>


