<template>
    <section>
        <h1 data-v-events>エントリ一覧</h1>
        <p>皆様の近況等、ご自由に記載ください。</p>
        <div style="margin-bottom:50px;">
            <h2 v-for="(entry, index) in entries" :key="index">
                <router-link v-bind:to="'/entries/detail/' + entry.no">{{ entry.title }}</router-link>
                <h3>Author：<router-link v-bind:to="'/profile/' + entry.user">{{ entry.userId }}</router-link></h3>
                <h3>Date：{{entry.dat}}</h3>
            </h2>
        </div>

        <router-link to='/entries/registration'><div class="buttonBase">新規登録</div></router-link>

    </section>
</template>

<script>
export default {
    name : "Entries",

    computed: {
        entries() {
            return this.$store.getters.entries;
        }
    },

    created()
    {
        this.$store.dispatch('initEntrylist')
    },

}
</script>

<style scoped>
    h1{
        font-size: 150%;
    }

    h2{
        font-size: 120%;
        font-weight: normal;
        margin: 15px 0px 3px 10px;
    }

    h3{
        font-size: 80%;
        font-weight: normal;
        margin: 3px 0px 0px 10px;
    }
</style>


