<template>
    <article>
        <p>招待コードを入力してください。</p>
        <p>招待コードをお持ちでない方は、OB会WEBページから発行してください。</p>

        <input type="text" name="invitationCd" placeholder="Invitation Code" v-model="invCode" data-v-registration>

        <div class="buttonBase" v-on:click="toInput">次へ</div>
    </article>
</template>

<script>
export default {
    name : "InputInvitation",

    data(){
        return{
            invCode : ""
        }
    },

    created()
    {
        // URLパラメータ取得
        let query = this.$route.query
        console.log(JSON.stringify(query))
        if(query != null && query.invCode != null)
        {
            this.invCode = query.invCode
        }
    },

    methods:{
        toInput(){
            this.$store.dispatch('setInvCode', this.invCode)
            this.$router.push({name:'reg-input'})
        }
    }
}
</script>


