<template>
  <transition name="editloc">
    <div class="editloc-mask" data-v-editloc>
        <div class="editloc-wrapper" data-v-editloc>
        <div class="editloc-container" data-v-editloc>
            <div class="editloc-header" data-v-editloc>
                Edit Locations
            </div>
            
            <SinglePosMap 
                ref="map"
            />

            <div class="editloc-body" data-v-editloc>
                <input type="text" name="posName"  placeholder="最寄の場所のキーワードを入力してください" v-model="inputTxt" v-on:keyup.enter="enterPos" data-v-editloc>
                <div class='buttonBase buttonEditloc' v-on:click = "clickUpdate" data-v-editloc>変更</div>
            </div>
            <div class="editloc-footer" data-v-editloc>
                <!-- <div class='buttonBase' v-on:click = "clickOk">更新</div> -->
                <div class='buttonBase' v-on:click = "clickClose">閉じる</div>
                <!-- <div class='buttonBase' style='float:left; margin:10px; width:75px' v-on:click = "clickOk">OK</div> -->
            </div>
        </div>
        </div>
    </div>
  </transition>
</template>

<script>
import  SinglePosMap from '@/widgets/SinglePosMap'

export default {
    name : "editloc",

    props: ['pos', 'index'],

    components :{
        SinglePosMap
    },

    data(){
        return{
          inputTxt : ""
        }
    },

    mounted(){
      this.inputTxt = this.pos[this.index].name
    },

    methods:{
        async clickUpdate(){
            await this.$store.dispatch('updateResidence', {name : this.inputTxt, index : this.index, save : true})
            this.pos[this.index].name = this.inputTxt
            this.$emit('editloc-close-event')
        },

        clickClose(){
            this.$emit('editloc-close-event')
        },

        async enterPos(){
            await this.$store.dispatch('updateResidence', {name : this.inputTxt, index : this.index, save : false})
            this.$refs.map.initMap()
        }
    }
}
</script>


<style scoped>
    h1[data-v-editloc] {
        font-size: 100%;
        font-weight: normal;

        margin: 10px 0px 0px 10px;
    }

    input[data-v-editloc]{
        margin: 5px 0px 0px 10px;
    }

    .buttonEditloc[data-v-editloc]
    {
        margin: 10px 0px 10px 0px;
    }

    .checkBoxInput[data-v-editloc]{
        width: unset;
        height: unset;
    }

 .editloc-mask[data-v-editloc] {
    position: fixed;
    z-index: 9999;
    top: 0;
    left: 0;
    width: 100%;
    height: 120%;
    background-color: rgba(0, 0, 0, .5);
    display: table;
    transition: opacity .3s ease;
  }
  
  .editloc-wrapper[data-v-editloc] {
    display: table-cell;
    vertical-align: middle;
  }
  
  .editloc-container[data-v-editloc] {
    width: 95%;
    max-width: 1200px;
    margin: 0px auto;
    margin-top:-20%;
    background-color: #fff;
    border-radius: 2px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
    transition: all .3s ease;
    /* font-family: Helvetica, Arial, sans-serif; */
  }

  .editloc-header[data-v-editloc] {
    padding: 1px 0px 1px 15px;
    color: #FFFFFF;
    background-color : #00a7ce;
  }
  .editloc-body[data-v-editloc] {
    margin: 0px 0px 0px 0px;
    padding: 10px 30px;
    height: auto;
    overflow: hidden;
  }
  .editloc-footer[data-v-editloc]{
    padding: 0px 30px 20px 30px;
    overflow: hidden;
  }

  .editloc-enter {
    opacity: 0;
  }
  
  .editloc-leave-active {
    opacity: 0;
  }
  
  .editloc-enter .editloc-container,
  .editloc-leave-active .editloc-container{
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
  }
  </style>


