<template>
    <div>
        <header>
            <h1><span class="logo">@</span><span class="logoTxt"><router-link to='/'>TJC</router-link></span></h1>
        </header>
        <div id="mnavToggle" v-if="this.showToggle" v-on:click="openSidebar">
            <div>
                <span />
                <span />
                <span />
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name : "Header",
    computed : {
        appTitle()
        {
            return this.$store.getters.appTitle
        },
        
        showToggle()
        {
            return this.$store.getters.showToggre
        }
    },
    methods :{
        openSidebar(){
            this.$store.dispatch('setShowSidebar', true)
        }
    }
}
</script>


<style>
/*  --  Start of Header setting -- */
    header{
        border-bottom : solid 1px #d9d9d9;
    }
    header h1{
        text-align: center;
        /* padding-left: 40%; */
        font-family: 'Carme', Segoe UI, "メイリオ", Meiryo, sans-serif;
        font-size: 150%;
        margin: 5px 0px 15px 0px;
    }
    .logo {
        color: #00a7ce;
        font-weight: bolder;
        font-size: 130%;
        font-family: 'Open Sans', Segoe UI, "メイリオ", Meiryo, sans-serif;
    }
    .logoTxt {
        line-height: 100%;
        margin-left: 5px;
    }
    .logoTxt a{
        border-bottom: none;
        color:#737373;
    }

/*  --  End of Header setting -- */

/*  --  Start of Toggle button setting -- */
    #mnavToggle {
        width: 45px;
        height: 40px;
        cursor: pointer;
        float: left;
        margin: 3px 15px 15px 15px;
        position: absolute;
        top:10px;
        display: block;
        
    }
    #mnavToggle span {
        display: block;
        position: absolute;
        width: 100%;
        border-bottom: solid 4px #00a7ce;
    }
    #mnavToggle span:nth-child(1) {
        top: 0;
    }
    #mnavToggle span:nth-child(2) {
        top: 15px;
    }
    #mnavToggle span:nth-child(3) {
        top: 30px;
    }
    #mnavToggle div {
        position: relative;
    }
/*  --  End of Toggle button setting -- */
</style>



