<template>
    <div>
        <h1>ユーザー管理</h1>
        <h2 data-v-lgu>ログインユーザー情報</h2>
        <table width="100%" data-v-lgu>
            <tr data-v-lgu>
                <th width="30%" data-v-lgu>loginId</th>
                <th width="30%" data-v-lgu>ログイン日時</th>
                <th width="30%" data-v-lgu>トークン有効期限</th>
            </tr>
            <tr v-for="(loginUser, index) in loginUsers" :key="index" data-v-lgu>
                <td data-v-lgu>{{loginUser.loginId}}</td>
                <td data-v-lgu>{{loginUser.regDt}}</td>
                <td data-v-lgu>{{loginUser.expDt}}</td>
            </tr>
        </table>

        <h2 data-v-lgu>招待メール送信</h2>
        <InvitationComp />
    </div>
</template>

<script>
import InvitationComp from '@/components/InvitationComp'
export default {
    name : "LoginedUser",

    components :{
        InvitationComp
    },

   computed: {
        loginUsers:{
            get()
            {
                return this.$store.getters.adminLoginUsers
            }
        }
    },

    created()
    {
        this.$store.dispatch('GetAdminLoginUsers')
        
        // 管理者チェック
        if(!this.$store.getters.userInfo.admin)
        {
            this.$router.push('/')
        }
    },
    
    data: function () {
        return {
        }
    }

}
</script>

<style scoped>
    h2[data-v-lgu] {
        font-size: 110%;
        font-weight: normal;
        color: #00a7ce;

        margin: 20px 0px 10px 0px;
    }

    h3[data-v-lgu] {
        font-size: 100%;
        font-weight: normal;

        margin: 30px 0px 0px 0px;
    }
    
    .editBox[data-v-lgu]{
        margin-bottom: 70px;
    }

    th[data-v-lgu]{
        text-align: left;
        font-weight: normal;
        padding : 5px 0px;
    }

    td[data-v-lgu] {
        padding:10px 10px;
    }


</style>


