<template>
    <div>
        <input type="text" v-model="input.comment" style="margin-bottom:20px;">
        <input type="checkbox" v-model="input.twitter" style="width:unset; height:unset;"><label><a href="https://twitter.com/tjc_ob_app" target="_blank">@TJC twitter</a>へ投稿する</label>
        <div class="buttonBase" v-on:click="addComment" style="margin-top:25px;">コメント追加</div>
        
        <div v-for="(comment, index) in comments" :key="index" class="comments" data-v-tl>
            <p><span style="font-size:90%;"><router-link v-bind:to="'/profile/' + comment.user.userNo">{{comment.user.name}}</router-link> {{comment.footer}}</span></p>
            <span>{{comment.body}} <a v-if="comment.editable" v-on:click="delComment(comment.cmtId)" style="font-size:90%;">削除</a></span>
        </div>
    </div>
</template>

<script>
export default {
    name : "TimeLineComp",

    props: ['limit'],

    computed: {
        comments(){
            return this.$store.getters.comments
        }
    },

    created()
    {
        this.$store.dispatch('GetComments', this.limit)
    },

    methods:{
        async addComment()
        {
            await this.$store.dispatch('AddTLcomment', this.input)
            await this.$store.dispatch('GetComments', this.limit)
            this.input.comment = null
        },

        async delComment(id)
        {
            await this.$store.dispatch('DelComment', id)
            await this.$store.dispatch('GetComments', this.limit)
        }

    },

    data: function () {
        return {
            input:{
                comment : null,
                twitter : false
            }
        }
    }
}
</script>

<style scoped>
    .comments[data-v-tl]
    {
        border-bottom: solid 1px #d9d9d9;
        padding : 20px 5px;
    }

</style>


